<template>
<div style="min-height:500px;margin-bottom:10px;">
    <el-table
    height="500"
    :data="ScApplyInfo"
    style="width: 100%;margin-bottom:50px;">

        <el-table-column
        label="竞赛名称"
        width="180">
        <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.scName }}</span>
        </template>
        </el-table-column>

        <el-table-column
        label="指导老师"
        width="180">
        <template slot-scope="scope">
            <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.teacher.name }}</el-tag>
            </div>
        </template>
        </el-table-column>

        <el-table-column
        label="所属学院"
        width="180">
        <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.college.collegeName}}</span>
        </template>
        </el-table-column>

        <el-table-column
        label="报名时间"
        width="200">
        <template slot-scope="scope">
            <i class="el-icon-time"></i>
            <span style="margin-left: 10px">{{ scope.row.createTime }}</span>
        </template>
        </el-table-column>

    <el-table-column
        label="审核状态"
        width="180">
        <template slot-scope="scope">
            <div slot="reference" class="name-wrapper">
                <el-tag v-if="scope.row.status==1" type="success" size="medium">已审核</el-tag>
                <el-tag v-else type="danger" size="medium">未审核</el-tag>

            </div>
        </template>
    </el-table-column>
  

        <el-table-column label="操作">
        <template slot-scope="scope">
            <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
            <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
        </el-table-column>
  </el-table>


</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    ScApplyInfo: [
        {
          id:0,
          scId: 0,
          scName:'技能竞赛名称',
          teacher:{id:1,name:'张三老师'},
          college:{id:1,collegeName:'所属学院名'},
          createTime: '2016-05-02',
          status:1
        }
        ]
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {

},
//方法
methods: {
        getScApplyInfo(){
         let id=JSON.parse(cookie.get('student')).id;
         if(id){
            request({
                url:`/scApply/studentScApplyList/${id}`,
                method:'get'
            }).then(res=>{
                this.ScApplyInfo=res.data.ScApplyInfo
            }).catch(err=>{
                
            })
         }
           
        },

     handleEdit(index, row) {
        console.log(index, row);
         this.$router.push({
            name: 'apply',
            params: {
            collegeId:row.college.id,
            scId:row.scId,
            isUpdate:true,
            applyId:row.id
            }
        })
      },
      handleDelete(index, row) {
        console.log(index, row);
        console.log("被删除的id",row.id)

         this.$confirm('此操作将永久删除该申请表, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            console.log("确定")
            request({
                url:`/scApply/delete/${row.id}`,
                method:'delete'
            }).then(res=>{
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
                this.getScApplyInfo();
            }).catch(err=>{
                 this.$message({
                    type: 'error',
                    message: '删除失败!'
                });
            })
         
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });  
        });
      }

},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
this.getScApplyInfo();
}
};
</script>

<style scoped>
</style>