<template>
<!-- background-color:#DDDDDD -->
<div style="min-height:500px;margin-bottom:10px;">

   <el-select v-model="scId" placeholder="请选择竞赛" @change="scChange">
    <el-option
      v-for="item in studentScInfoList"
      :key="item.id"
      :label="item.scName"
      :value="item.id">
    </el-option>
  </el-select>

   <el-select v-model="epId" placeholder="请选择考核项目" @change="epChange">
    <el-option
      v-for="item in examProjectList"
      :key="item.id"
      :label="item.projectName"
      :value="item.id">
    </el-option>
  </el-select>

    <div id="echarts-main" style="width: 100%;height: 600px;">
    </div>



</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../../utils/request"
let echarts = require('echarts');
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
  studentId:"",
  studentScInfoList:[],
  scId:"",
  examProjectList:[],
  epId:"",
  scExamList:[],
  charts: '成绩',
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {

},
//方法
methods: {
  getStudentScInfoList(){
      let id=JSON.parse(cookie.get('student')).id;
         if(id){
           this.studentId=id;
             request({
                 url:`/scInfo/studentScInfoList/${id}`,
                 method:'get'
             }).
             then(res=>{
                    this.studentScInfoList=res.data.studentScInfoList
             }).catch(err=>{
                 console.log(err)
             })
         }
  },
  getExamProjectList(scId){
    request({
      url:`/examProject/list/${scId}`,
      method:'get'
    }).then(res=>{
      this.examProjectList=res.data.examProjectList
    }).catch(err=>{
      console.log(err);
      
    })
  },
  scChange(scId){
    this.epId=""
    this.examProjectList=[];
    echarts.init(document.getElementById('echarts-main')).dispose();

    this.getExamProjectList(scId)
  },
  epChange(epId){
    this.scExamList=[];
      echarts.init(document.getElementById('echarts-main')).dispose();
    request({
      url:`/scExam/list/${this.studentId}/${this.scId}/${epId}`,
      method:'get'
    }).then(res=>{
      if(res.code!=500){
              this.scExamList=res.data.scExamList;
              this.drawing();
      }else{
         this.$message({
              showClose: true,
              message: '占无成绩',
              type: 'warning'
          });
      }
    }).catch(err=>{
      console.log(err);
    })
  },
  drawing(){
    let titleText="";
    for(let i=0;i<this.studentScInfoList.length;i++){
      if(this.studentScInfoList[i].id==this.scId){
          titleText+=this.studentScInfoList[i].scName+"：";
      }
    }
    for(let j=0;j<this.examProjectList.length;j++){
      if(this.examProjectList[j].id==this.epId){
          titleText+=this.examProjectList[j].projectName+"考核";
      }
    }
    
    let xAxisData=[];
    for(let i=0;i<this.scExamList.length;i++){
      xAxisData.push("第"+this.scExamList[i].examIndex+"次考核")
    }
    let seriesData=[];
    for(let i=0;i<this.scExamList.length;i++){
      seriesData.push(this.scExamList[i].score)
    }
     // 基于准备好的dom，初始化echarts实例
    let myChart = echarts.init(document.getElementById('echarts-main'),'dark');
    // 绘制图表
    myChart.setOption({
      title: {
        text: titleText,//图标标题
        x: 'center'//水平居中
      },
      tooltip: {//提示框信息
        axisPointer: {
            type: 'shadow'
        }
      },
    
      xAxis: {
          name:'次序',
          type: 'category',
          data: xAxisData,
          
      },
      yAxis: {
          name:'分',
          type: 'value',
          min:0,//坐标起始值
          max:100, //坐标最大值
      },
      series: [{
          data: seriesData,
          type: 'line',
          itemStyle:{ 
                normal:{ color: "#d14a61" } //坐标圆点的颜色
          }, 
          lineStyle:{ 
                normal:{ color: "green"  }//线条的颜色及宽度
          },
          label: {//线条上的数字提示信息
            normal: {
                show: true,
                position: 'top',
                color: "#d14a61",
                fontWeight: "bold"
              },
          },
          
      }]
    });

  }

},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
  this.getStudentScInfoList();
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {
 
},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
</style>