<template>
<div style="min-height:500px;margin-bottom:10px;">
    <div style="width:500px;">
            <el-form ref="form" :model="studentInfo" label-width="80px">
                <el-form-item label="头像:">
                    <img :src="studentInfo.headerUrl" style="height:80px;width:80px;border-radius:40px"/>
                </el-form-item>

                <el-form-item label="姓名:">
                    <el-input v-model="studentInfo.name"></el-input>
                </el-form-item>
                <el-form-item label="性别">
                    <el-radio v-model="studentInfo.sex" label="男">男</el-radio>
                    <el-radio v-model="studentInfo.sex" label="女">女</el-radio>
                </el-form-item>
                <el-form-item label="手机号">
                    <el-input v-model="studentInfo.phone" :disabled="!isUpdate"></el-input>
                    <span @click="dialogVisible=true">我要修改它们！</span>
                </el-form-item>
                <el-form-item label="密码" >
                    <el-input v-model="studentInfo.password" :type="passwordShowType" :disabled="!isUpdate"></el-input>
                    <a class="iconfont icon-chakan" @click="showPassword"></a>
                </el-form-item>
                <el-form-item label="身份证号">
                    <el-input v-model="studentInfo.cdCard" :disabled="!isUpdate"></el-input>
                </el-form-item>
                <el-form-item label="qq">
                    <el-input v-model="studentInfo.qq"></el-input>
                </el-form-item>
               
                <el-form-item label="班级">
                    <el-select v-model="studentInfo.collegeId" @change="collegeChange">
                        <el-option :label="item.collegeName" :value="item.id" v-for="item in collegeList" :key="item.id" ></el-option>
                    </el-select>

                    <el-select v-model="studentInfo.classId">
                        <el-option :label="item.className" :value="item.id" v-for="item in classList" :key="item.id"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="updateStudentInfo">修改信息</el-button>
                </el-form-item>
             </el-form>
            </div>

            <el-dialog
            title="用户验证"
            :visible.sync="dialogVisible"
            width="30%"
            center>

            <div>请用手机号:{{studentInfo.phone}} 接收验证码</div>
            <div>
                <input type="text" v-model="inputCode">
                <button @click="sendCode">发送验证码</button>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button @click="closeDialog">取 消</el-button>
                <el-button type="primary" @click="validationPhoneCode">确 定</el-button>
            </span>
            </el-dialog>


</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    activeName: 'first',
    isUpdate:false,
    passwordShowType:'password',
    dialogVisible:false,
    studentPhoneCode:'',//接收到的验证码
    inputCode:'',//输入的验证码
    studentInfo:{
        id:'1',
        name:'范征远',
        sex:'男',
        phone:'13142211138',
        password:'123456789',
        cdCard:'430121200011102816',
        qq:'1954418428',
        headerUrl:'https://cptmanagement-fzy.oss-cn-beijing.aliyuncs.com/2020-11-06/4735f0fa36b74bd7b34f6a90aa9da836QQ.jpg',
        collegeId:2,
        classId:3
    },
    collegeList:[
            // {id:1,collegeName:'信息学院',classList:[{id:1,className:'软测1班'},{id:2,className:'软测2班'},{id:3,className:'软测3班'},{id:4,className:'大数据1班'},{id:4,className:'大数据2班'}]},
            // {id:2,collegeName:'机械学院',classList:[{id:1,className:'机械1班'},{id:2,className:'机械2班'},{id:3,className:'机械3班'},{id:4,className:'机械4班'}]},
            // {id:3,collegeName:'商务学院',classList:[{id:1,className:'商务1班'},{id:2,className:'商务2班'},{id:3,className:'商务3班'},{id:4,className:'商务4班'}]}
    ],
    classList:[]
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {
// 'studentInfo.collegeId':function(){
//     this.refreshClassList();
// }
},
//方法
methods: {
        getCollegeAndClassList(){
            //拿学院以及旗下班级 列表
            request({
                url:`/college/collegeList`,
                method:'get',
            }).then(res=>{
                this.collegeList=res.data.collegeList
                this.refreshClassList()
            }).catch(err=>{
                console.log(err)
            })
        },
        getStudentInfo(){
         let id=JSON.parse(cookie.get('student')).id;
        //  console.log("此用户id",id);
            request({
                url:`/student/get/${id}`,
                method:'get'
            }).then(res=>{
                this.studentInfo=res.data.studentInfo
                this.refreshClassList();
            }).catch(err=>{
            })

        },
        handleClick(tab, event) {
            console.log(tab, event);
        },
        collegeChange(v){
               console.log(v);
               this.refreshClassList();
               this.studentInfo.classId=this.classList[0].id
        },
        refreshClassList(){
             for(let i=0;i<this.collegeList.length;i++){
                if(this.collegeList[i].id==this.studentInfo.collegeId){
                     this.classList=this.collegeList[i].classList;
                    break;
                }
            }
        },
        showPassword(){
            if(this.passwordShowType=='password'){
                this.passwordShowType='input';
            }else{
                this.passwordShowType='password';
            }
        },
        sendCode(){
            console.log("发送验证码了")
            //发送请求拿验证码
            request({
                url:`/sms/send/${this.studentInfo.phone}`,
                method:'get'
            }).then(res=>{
                this.studentPhoneCode=res.data.code;
            }).catch(err=>{
                console.log(err);
            })

        },
        validationPhoneCode(){
            if(this.inputCode == this.studentPhoneCode && this.studentPhoneCode!=""){
                this.isUpdate=true;
                 this.$message({
                    showClose: true,
                    message: '验证码正确',
                    type: 'success'
                });
            }else{
                this.inputCode=""
                 this.$message({
                    showClose: true,
                    message: '验证码错误',
                    type: 'error'
                });
            }
            this.dialogVisible=false;
            
        },
        closeDialog(){
            this.inputCode="";
            this.dialogVisible=false;
        },

        updateStudentInfo(){
            let {id,name,sex,phone,password,cdCard,qq,headerUrl,collegeId,classId}=this.studentInfo
            request({
                url:'/student/update',
                method:'put',
                data:{
                    id,name,sex,phone,password,cdCard,qq,headerUrl,collegeId,classId
                }
                    
            }).then(res=>{
              if(res.code!=500){
                this.$message({
                    showClose: true,
                    message: '修改成功',
                    type: 'success'
                });
                this.getStudentInfo();
              }else{
                this.$message({
                    showClose: true,
                    message: res.msg,
                    type: 'error'
                });
              }
                
            }).catch(err=>{
                console.log(err);
                
            })

        }
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
this.getCollegeAndClassList();
this.refreshClassList();



this.getStudentInfo();
},
};
</script>

<style scoped>
.tab-list{
    width: 1280px;
    margin: 10px auto;
}
.el-form-item{
    margin: 5px;
}
</style>