<template>
<div>
        <el-card shadow="always" class="elcard">
            <el-table
              :data="teacherWinningInfoList"
              style="width: 100%">

              <el-table-column
                label="获奖竞赛"
                width="180">
                <template slot-scope="scope">
                  <span>{{ scope.row.scInfo.scName}}</span>
                </template>
              </el-table-column>

              <el-table-column
                label="获奖名称"
                width="180">
                <template slot-scope="scope">
                  <span>{{ scope.row.wiName }}</span>
                </template>
              </el-table-column>
              
              <el-table-column
                label="获奖描述"
                width="220">
                <template slot-scope="scope">
                  <span>{{ scope.row.description }}</span>
                </template>
              </el-table-column>
              
              <el-table-column
                label="获奖人员"
                width="200">
                <template slot-scope="scope">
                  <span>{{ scope.row.student.name }}</span>
                  <img :src="scope.row.student.headerUrl" alt="" class="header-img">
                </template>
              </el-table-column>


              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
        </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
import  cookie from 'js-cookie'
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
  teacher:{
        id: 1,
        name: "谢丁峰",
        sex: "男",
        phone: "11111111111",
        password: "666666",
        qq: "1954418428",
        headerUrl: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        officeName: "二教2101",
        isAdmin: 1,
        collegeId: 2,
        college: null,
        scInfoList: [],
        aclass: null
    },
    teacherWinningInfoList:[]
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {

},
//方法
methods: {
  getTeacherWinningInfoList(){
      request({
        url:`/winningInfo/teacherWinningInfoList/${this.teacher.id}`,
        method:'get'
      }).then(res=>{
          this.teacherWinningInfoList=res.data.teacherWinningInfoList
      })
  },
  handleEdit(index, row){
          this.$router.push('/adminMain/wiadd/'+row.id)
  },
  handleDelete(index,row){
          this.$confirm('此操作将永久该技能竞赛获奖信息 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
          }).then(() => {
            request({
                url:`/winningInfo/delete/${row.id}`,
                method:'delete'
            }).then(res=>{
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
                this.getTeacherWinningInfoList();
            }).catch(err=>{
                 this.$message({
                    type: 'error',
                    message: '删除失败!'
                });
            })
            })
  }
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
  this.getTeacherWinningInfoList()
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
.header-img{
    margin-left: 10px;
    height: 50px;
    width: 50px;
    border-radius: 25px;
    display: inline;
    vertical-align:middle;
}
</style>