<template>
<div>
    <el-card shadow="always" class="elcard">
            <div>{{title}}</div>
            <!-- :rules="rules" -->
            <el-form  :model="college" label-position="top"   ref="ruleForm" label-width="80px"  >
                <el-form-item label="学院名称" prop="collegeName">
                     <el-input v-model="college.collegeName"></el-input>
                </el-form-item>
                <el-form-item label="学院办公室" prop="officeName">
                     <el-input v-model="college.officeName"></el-input>
                </el-form-item>
                <el-form-item>

                        <el-button type="primary" v-if="!isUpdate" @click="submitForm('ruleForm')">确定添加</el-button>
                        <el-button type="success"  v-else  @click="submitForm('ruleForm')">确定修改</el-button>
                </el-form-item>
            </el-form>
    </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
import { MessageBox } from 'element-ui';
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    title:"添加学院信息",
    isUpdate:false,
    college: {
          collegeName: '',
          officeName: '',
    },
    rules:{
        collegeName:[
            { required: true, message: '请输入学院名称', trigger: 'blur' }
        ],
        officeName:[
            { required: true, message: '请输入学院办公室', trigger: 'blur' }
        ]
    }
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {
      '$route' (to, from) {
      // 对路由变化作出响应...
      if(to.path!=from.path){
            Object.assign(this.$data, this.$options.data())}
      }
},
//方法
methods: {
    addCollege(){
        request({
            url:'/college/add',
            method:'post',
            data:{
                collegeName:this.college.collegeName,
                officeName:this.college.officeName
            }
        }).then(res=>{
            console.log(res);
            if(res.code!=500){
                console.log("成功");
                this.$message({
                message: '添加学院信息成功',
                type: 'success'
                });
            }else{
                console.log("失败");
                this.$message({
                message: res.msg,
                type: 'error'
                });
            }
        }).catch(err=>{
            console.log(err);
        })
    },
    getCollegeById(id){
         request({
            url:`/college/get/${id}`,
            method:'get',
        }).then(res=>{
            this.college=res.data.college
        })
    },
    updateCollege(){
        request({
            url:'/college/update',
            method:'put',
            data:{
                id:this.college.id,
                collegeName:this.college.collegeName,
                officeName:this.college.officeName
            }
        }).then(res=>{
            console.log(res);
            if(res.code!=500){
                console.log("成功");
                this.$message({
                message: '修改学院信息成功',
                type: 'success'
                });
            }else{
                console.log("失败");
                this.$message({
                message: res.msg,
                type: 'error'
                });
            }
        }).catch(err=>{
            console.log(err);
        })
    },
        submitForm(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if(!this.isUpdate){
                        this.addCollege()
                    }else{
                        this.updateCollege()
                    }
                } else {
                    return false;
                }
                });
        },

},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
    if(this.$route.params&&this.$route.params.id){
        this.isUpdate=true;
        this.title="修改学院信息"
        this.getCollegeById(this.$route.params.id)
    }


},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
</style>