<template>
<div>

        <el-card shadow="always" class="elcard">
            <el-button @click="clearFilter">清除所有过滤器</el-button>
            <el-table
            ref="filterTable"
            :data="teacherList"
            style="width: 100%">

                <el-table-column
                label="姓名"
                width="180">
                    <template slot-scope="scope">
                        <img :src="scope.row.headerUrl"  class="header-img">
                        <span style="margin-left: 10px">{{ scope.row.name }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                label="性别"
                width="80">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.sex }}</span>
                    </template>
                </el-table-column>


                <el-table-column
                label="电话"
                width="150">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.phone }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                label="qq"
                width="180">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.qq }}</span>
                    </template>
                </el-table-column>


                <el-table-column
                label="办公室"
                width="150">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.officeName }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                label="学院"
                width="170"
                :filters="filters"
                :filter-method="filterTag"
                filter-placement="bottom-end">

                <template slot-scope="scope">
                    <el-tag
                    type="success"
                    disable-transitions>{{scope.row.college.collegeName}}</el-tag>
                </template>
                </el-table-column>

                 <el-table-column
                label="操作">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
                </el-table-column>
            </el-table>
        </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
        teacherList:[
            // {
            //  id: 1,
            //  name: "谢丁峰",
            //  sex: "男",
            //  phone: "11111111111",
            //  password: "666666",
            //  qq: "1954418428",
            //  headerUrl: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
            //  officeName: "二教2101",
            //  isAdmin: 1,
            //  collegeId: 1,
            //  college: {
            //         id: 1,
            //         collegeName: "信息工程学院",
            //         officeName: "1教1105",
            //         classList: [],
            //         scInfoList: [],
            //         teacherList: []
            //     },
            // scInfoList: [],
            // aclass: null
            //}
        ],
        filters:[]
    }
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {
    
},
//方法
methods: {
        getTeacherList(){
            request({
                url:'/teacher/list',
                method:'get'
            }).then(res=>{
                this.teacherList=res.data.teacherList
                this.getFilters()
            })
        
        },
       
        getFilters(){
                for(let i=0;i<this.teacherList.length;i++){
                let college=this.teacherList[i].college;
                let value=college.id;
                let text=college.collegeName;
                this.filters.push({text,value})
                }
                var arr=this.filters
                var result = [];
                var obj = {};
                for(var i =0; i<arr.length; i++){
                if(!obj[arr[i].value]){
                    result.push(arr[i]);
                    obj[arr[i].value] = true;
                }
                }
                this.filters=result
                
        },
        clearFilter() {
        this.$refs.filterTable.clearFilter();
        },
        filterTag(value, row) {
        return row.college.id === value;
        },
        handleEdit(index, row) {
            console.log(index, row);
            this.$router.push({
                path:`teacheradd/${row.id}`,
            })
        },
        handleDelete(index, row) {
            console.log(index, row);
            this.$confirm('此操作将永久删除老师信息,以及该老师下的班级、竞赛获奖信息、技能竞赛信息等,是否继续?', '危险警告', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'error',
            }).then(() => {
                    request({
                        url:`/teacher/delete/${row.id}`,
                        method:'delete'
                    }).then(res=>{
                            if(res.code!=500){
                                this.$message({
                                type: 'success',
                                message: '删除成功!'
                                });
                                this.getTeacherList();
                            }else{
                                this.$message({
                                type: 'error',
                                message: '删除失败!'
                                });
                            }
                        }).catch(err=>{console.log(err);})
                        
                }).catch(err=>{
                    this.$message({
                        type: 'warning',
                        message: '取消删除!'
                    });
                })
        }
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
this.getTeacherList()

},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
.header-img{
    height: 50px;
    width: 50px;
    border-radius: 25px;
    display: inline;
    vertical-align:middle;
    margin-left: 10px;
}
</style>