<template>
<div>
    <div style="min-height:500px;">
        <el-select v-model="collegeId" placeholder="请选择" @change="selectedChange" style="margin:10px 0px;">
            <el-option
            v-for="item in collegeList"
            :key="item.id"
            :label="item.collegeName"
            :value="item.id"
            >
            </el-option>
        </el-select>

        <div v-if="scInfoListLength!=0" class="sc-list">
            <el-card class="sc-item"   v-for="item in scInfoList" :key="item.id">
            <img src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png" class="image">
            <div>
                <span class="sc-name"> {{item.scName}}</span>
                <div style="position:relative">
                    <span class="sc-createtime">{{item.createTime}}</span>
                    <el-link class="look-details" :underline="false" type="primary" @click="lookDetails(item.id)">查看详情</el-link>
                </div>
            </div>
            </el-card>
        </div>
        <div v-else>
            <div>
                此学院还有发布任何技能竞赛
            </div>
        </div>


        

    </div>

</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    collegeId:'',
    collegeList:[
        {
            id:1,
            collegeName:'信息工程学院',
            classList:[
                    {
                        id: 1,
                        className: "2018软件测试-1班",
                        collegeId: 1,
                        teacherId: 1,
                        studentList: []
                    }
            ],
            scInfoList:[
                    {
                        id: 1,
                        scName: "第一个技能竞赛",
                        scSponsor: "主办方1",
                        collegeId: 1,
                        college: null,
                        description: "描述1描述1描述1描述1描述1描述1描述1描述1描述1描述1描述1描述1描述1描述1描述1描述1",
                        teacherId: 1,
                        teacher: {
                            id: 1,
                            name: "谢丁峰",
                            sex: "男",
                            phone: "11111111111",
                            password: "666666",
                            qq: "1954418428",
                            headerUrl: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
                            officeName: "二教2101",
                            isAdmin: 1,
                            collegeId: 1,
                            scInfoList: [],
                            aclass: null
                        },
                        status: 1,
                        startTime: "2020-11-06 08:00:00",
                        endTime: "2020-12-06 08:00:00",
                        createTime: "2020-11-06 20:17:12",
                        modifyTime: "2020-11-06 20:17:12"
            }]
        }
    ],
    scInfoList:[],
    scInfoListLength:0

};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {

},
//方法
methods: {
    getBaseInfo(){
        //获取基本的数据 学院及其院下竞赛列表 学生信息
        request({
            url:'/college/collegeList',
            method:'get'
        }).then(res=>{
            console.log("所有学院相关信息列表:",res.data.collegeList)
            this.collegeList=res.data.collegeList;
            this.selectedChange();
        }).catch(err=>{
        })
    },
    selectedChange(){
        for(let i=0;i<this.collegeList.length;i++){
            if(this.collegeId==this.collegeList[i].id){
                this.scInfoList=this.collegeList[i].scInfoList;
            }
        }
        this.scInfoListLength=this.scInfoList.length;
        //按时间倒序排序
        this.scInfoList.sort(this.compare("createTime"));
    },
    lookDetails(sc_id){
        this.$router.push(`/cptdetails/${sc_id}`)
    },
    //日期倒序
    compare(createTime){
        return function(a,b){
            var value1 = a[createTime];
            var value2 = b[createTime];
            return Date.parse(value2)-Date.parse(value1)
        }
    }
    

},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
    this.getBaseInfo();
    let {collegeId}=this.$route.params;
    // console.log(collegeId)
    if(collegeId){
        this.collegeId=collegeId;
    }else{
        this.collegeId=this.collegeList[0].id;
    }
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.sc-list{
    display: flex;
    justify-content:space-evenly;
    flex-wrap: wrap;
}
.sc-list:after {
    content: "";
    flex: auto;
 }

.sc-item{
    width: 340px;
    font-size: 18px;
    margin:0  1.5px 20px 1.5px ;
}
.sc-item img{
    width: 300px;
    height: 260px;
}

.sc-item .sc-name{
    margin: 10px 0;
    display: block;
}
.sc-item .look-details{
position: absolute;
top: 0;
right: 0;
font-size: 18px;
}

</style>