<template>
<div>
          <el-card shadow="always" class="elcard">
            <el-button @click="clearFilter">清除所有过滤器</el-button>
              <el-table
              ref="filterTable"
              :data="scApplyList"
              style="width: 100%">


              <el-table-column
              label="技能竞赛名称"
              :filters="filters"
              :filter-method="filterTag"

              filter-placement="bottom-end"
              width="150">
                  <template slot-scope="scope">
                      <span>{{ scope.row.scInfo.scName }}</span>
                  </template>
              </el-table-column>

              <el-table-column
              label="申请人"
              width="200">
                  <template slot-scope="scope">
                        <img :src="scope.row.student.headerUrl" class="header-img">
                        <span>{{ scope.row.student.name}}</span>
                  </template>
              </el-table-column>

                <el-table-column
              label="学院"
              width="150"
              :filters="filters2"
              :filter-method="filterTag2"
              >
                  <template slot-scope="scope">
                        <span>{{ scope.row.student.college.collegeName}}</span>
                  </template>
              </el-table-column>

              <el-table-column
              label="报名时间"
              width="200">
              <template slot-scope="scope">
                  <i class="el-icon-time"></i>
                  <span>{{ scope.row.createTime }}</span>
              </template>
              </el-table-column>

             <el-table-column
              label="审核状态"
              width="120">
              <template slot-scope="scope">
                  <div slot="reference" class="name-wrapper">
                      <el-tag v-if="scope.row.status==1" type="success" size="medium">已审核</el-tag>
                      <el-tag v-else type="danger" size="medium">未审核</el-tag>
                  </div>
              </template>
             </el-table-column>

             <el-table-column
              label="申请文件"
              width="150">
              <template slot-scope="scope">

                <form :action="scope.row.applyFile" v-if="scope.row.applyFile">
                    <intput type="hidden" name="Content-Disposition" :value="'attachment;filename='+scope.row.applyFile.slice(54)"></intput>
                    <button  style="height:25px"  type="primary">下载此申请文件</button>
                </form>
                <el-tag v-else>无申请文件</el-tag>
              </template>
             </el-table-column>

              <el-table-column
              label="操作">
              <template slot-scope="scope">
                 <el-button type="success" @click="updateStatus(scope.row.id)" size="mini">审核</el-button>

                  <el-button
                  size="mini"
                  type="warning"
                  :disabled="!scope.row.status==1"
                  @click="doStudentExam(scope.row.student.id)">考核打分</el-button>

              </template>
              </el-table-column>
              </el-table>
          </el-card>

          
        <el-dialog title="学生考核信息" :visible.sync="showStudentExamDialog" @close="clearExamInfoList" width="80%">
            <div style="margin-bottom:10px">
              <img :src="studentInfo.headerUrl" class="header-img">
              <span>{{studentInfo.name}}</span> 
            </div>
            
            <el-select v-model="scId" placeholder="请选择竞赛" @change="scChange">
              <el-option
                v-for="item in finalScInfoList"
                :key="item.id"
                :label="item.scName"
                :value="item.id">
              </el-option>
            </el-select>

            <el-select v-model="epId" placeholder="请选择考核项目" @change="epChange">
              <el-option
                v-for="item in examProjectList"
                :key="item.id"
                :label="item.projectName"
                :value="item.id">
              </el-option>
            </el-select>

               <el-button
                size="mini" type="primary"  :disabled="!epId" style="margin-left:10px"
                @click="addExam">添加考核信息</el-button>

             <el-table
              height="500"
              :data="scExamList"
              style="width: 100%;margin-bottom:50px;">

              <el-table-column
              label="次序"
              width="180">
              <template slot-scope="scope">
                  <span >{{"第"+scope.row.examIndex+"次考核"}}</span>
              </template>
              </el-table-column>

              <el-table-column
              label="分数"
              width="180">
              <template slot-scope="scope">
                  <span >{{ scope.row.score }}</span>
              </template>
              </el-table-column>

              <el-table-column
              label="描述"
              width="180">
              <template slot-scope="scope">
                  <span >{{ scope.row.description }}</span>
              </template>
              </el-table-column>

              <el-table-column
                label="操作">
                <template slot-scope="scope">

                    <el-button
                    size="mini"
                    @click="updateExam(scope.row)">修改</el-button>

                    <el-button
                    size="mini"
                    type="danger"
                    @click="deleteExam(scope.row.id)">删除</el-button>

                </template>
                </el-table-column>

            </el-table>
           
        </el-dialog>

        <el-dialog :title="isAddExamInfo?'添加考核信息':'修改考核信息'" :visible.sync="addOrUpdateExamDialog" @close="clearExamInfo" width="80%">

          <el-form  :model="ExamInfo"  label-width="120px" label-position="right" style="width:600px">
            <el-form-item label="次序:">
              <el-input v-model="ExamInfo.examIndex" :disabled="true"></el-input>
            </el-form-item>

            <el-form-item label="成绩:">
              <el-input v-model="ExamInfo.score" placeholder="输入分数0~100"></el-input>
            </el-form-item>

            <el-form-item label="获奖描述:" >
              <el-input v-model="ExamInfo.description"></el-input>
            </el-form-item>

            <el-form-item>
              <el-button :type="isAddExamInfo?'primary':'success'"  @click="addOrUpdateSubmit">确定{{isAddExamInfo?'添加':'修改'}}</el-button>
            </el-form-item>
          </el-form>
        </el-dialog>


</div>
</template>

<script>
import request from "../../../utils/request"
import  cookie from 'js-cookie'
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
  teacher:{},
  scApplyList:[],
  filters:[],
  filters2:[],

  showStudentApplyListDialog:false,
  StudentApplyList:[],

  showStudentExamDialog:false,
  scId:"",
  epId:"",
  teacherScInfoList:[],
  examProjectList:[],
  scExamList:[],

  studentApplyIds:[],
  finalScInfoList:[],

  ExamInfo:{},
  addOrUpdateExamDialog:false,
  isAddExamInfo:false,

    studentInfo:{},

};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {

},
//方法
methods: {
  getTeacherScApplyList(){
    request({
      url:`/scApply/teacherScApplyList/${this.teacher.id}`,
      method:'get'
    }).then(res=>{
        this.scApplyList=res.data.scApplyList
        this.getFilters();
        this.getFilters2();
    })
  },

   getFilters(){
      for(let i=0;i<this.scApplyList.length;i++){
      let scInfo=this.scApplyList[i].scInfo;
      let value=scInfo.id;
      let text=scInfo.scName;
      this.filters.push({text,value})
      }

      var arr=this.filters
      var result = [];
      var obj = {};
      for(var i =0; i<arr.length; i++){
      if(!obj[arr[i].value]){
          result.push(arr[i]);
          obj[arr[i].value] = true;
      }
      }
      this.filters=result
    },
     getFilters2(){
      for(let i=0;i<this.scApplyList.length;i++){
      let college=this.scApplyList[i].student.college;
      let value=college.id;
      let text=college.collegeName;
      this.filters2.push({text,value})
      }

      var arr=this.filters2
      var result = [];
      var obj = {};
      for(var i =0; i<arr.length; i++){
      if(!obj[arr[i].value]){
          result.push(arr[i]);
          obj[arr[i].value] = true;
      }
      }
      this.filters2=result
    },

    filterTag(value, row) {
        return row.scInfo.id === value;
    },
    filterTag2(value, row){
        return row.student.college.id === value;
    },
  clearFilter() {
    this.$refs.filterTable.clearFilter();
  },  
  updateStatus(scApplyId){
    request({
      url:`/scApply/approval/${scApplyId}`,
      method:'put'
    }).then(res=>{
        if(res.code!=500){
          this.$message({
            type: 'success',
            message: '审核成功'
          });  
        }else{
          this.$message({
            type: 'error',
            message: '审核失败'
          });  
        }
    }).catch(err=>{

    })
  },

  clearExamInfo(){
    this.ExamInfo={};
  },
  clearApplyInfo(){
    this.StudentApplyList=[];
  },
  clearExamInfoList(){
    this.scId="",
    this.epId="",
    this.scExamList=[];
    this.studentInfo={};
    this.examProjectList=[];
    this.studentApplyIds=[];
    this.finalScInfoList=[];
  },
  updateExam(exam){
      this.isAddExamInfo=false;
      console.log(exam);
      this.addOrUpdateExamDialog=true;
      this.ExamInfo=exam;
  },
  addExam(){
    this.isAddExamInfo=true;
    this.addOrUpdateExamDialog=true;
    this.ExamInfo.examIndex=1;
    if(this.scExamList[this.scExamList.length-1]){
        this.ExamInfo.examIndex=this.scExamList[this.scExamList.length-1].examIndex+1;
    }
  },
  deleteExam(exId){
      console.log(exId);
      request({
        url:`/scExam/delete/${exId}`,
        method:'delete'
      }).then(res=>{
          if(res.code!=500){
                this.$message({
                  showClose: true,
                  message: '删除成功',
                  type: 'success'
              });
              this.epChange(this.epId);
            }else{
                this.$message({
                  showClose: true,
                  message: '删除失败',
                  type: 'error'
              });
            }
      }).catch(error=>{
        console.log(error);
        
      })
  },
  addOrUpdateSubmit(){
      if(this.isAddExamInfo){
        this.ExamInfo.scId=this.scId;
        this.ExamInfo.studentId=this.studentInfo.id;
        this.ExamInfo.epId=this.epId;
        request({
          url:'/scExam/add',
          method:'post',
          data:this.ExamInfo
        }).then(res=>{
            if(res.code!=500){
                this.$message({
                  showClose: true,
                  message: '添加成功',
                  type: 'success'
              });
              this.epChange(this.epId);
            }else{
                this.$message({
                  showClose: true,
                  message: '添加失败',
                  type: 'error'
              });
            }
        }).catch(err=>{
          console.log(err);
          
        })

      }else{
          request({
            url:'/scExam/update',
            method:'put',
            data:this.ExamInfo
          }).then(res=>{
            if(res.code!=500){
                this.$message({
                  showClose: true,
                  message: '修改成功',
                  type: 'success'
              });
            }else{
                this.$message({
                  showClose: true,
                  message: '修改失败',
                  type: 'error'
              });
            }
          }).catch(err=>{
            console.log(err);
          })
      }
      this.addOrUpdateExamDialog=false;
  },
  doStudentExam(studentId){
    console.log("学生id：",studentId);
  request({
        url:`/scInfo/studentScInfoList/${studentId}`,
        method:'get'
    }).
    then(res=>{
      this.studentApplyIds=res.data.studentScInfoList
      for(let i=0;i<this.studentApplyIds.length;i++){
        for(let j=0;j<this.teacherScInfoList.length;j++){
          if(this.studentApplyIds[i].id==this.teacherScInfoList[j].id){
            this.finalScInfoList.push(this.teacherScInfoList[j])
          }
        }
      }
    }).catch(err=>{
        console.log(err)
    })


    request({
      url:`/student/get/${studentId}`,
      method:'get'
    }).then(res=>{
      this.studentInfo=res.data.studentInfo
    }).catch(err=>{
        console.log(err);
    })

    this.showStudentExamDialog=true;
  },
  getExamProjectList(scId){
    request({
      url:`/examProject/list/${scId}`,
      method:'get'
    }).then(res=>{
      this.examProjectList=res.data.examProjectList
    }).catch(err=>{
      console.log(err);
    })
  },
  scChange(scId){
    this.epId=""
    this.getExamProjectList(scId)
  },
  epChange(epId){
    this.scExamList=[];
    request({
      url:`/scExam/list/${this.studentInfo.id}/${this.scId}/${epId}`,
      method:'get'
    }).then(res=>{
      if(res.code!=500){
              this.scExamList=res.data.scExamList;
      }else{
         this.$message({
              showClose: true,
              message: '占无成绩',
              type: 'warning'
          });
      }
    }).catch(err=>{
      console.log(err);
    })
  },
  getteacherScInfoList(){
    request({
      url:`/scInfo/teacherScInfoList/${this.teacher.id}`,
      method:'get'
    }).then(res=>{
      this.teacherScInfoList=res.data.teacherScInfoList;
    }).catch(err=>{
      console.log(err);
    })
  },
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
    if(cookie.get("teacher")){
      let teacherInfo=cookie.get("teacher");
      if(teacherInfo){
        this.teacher = JSON.parse(teacherInfo)
        this.getTeacherScApplyList();
      } 
    }
    this.getteacherScInfoList();


},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
.header-img{
    height: 50px;
    width: 50px;
    border-radius: 25px;
    display: inline;
    vertical-align:middle;
    margin-right: 10px;
}
</style>