import Vue from 'vue'
import Router from 'vue-router'
import cookie from 'js-cookie'
import ElementUI from 'element-ui'


import index from './views/index'
import login from './views/System/login'
import page404 from './views/System/404'


import cptlist from './views/student/cptlist/cptlist'

import collegecptlist from './views/student/collegecptlist/collegecptlist'
import winninginfolist from './views/student/winninginfolist/winninginfolist'
import winninginfodetails from './views/student/winninginfolist/winninginfodetails'

import apply from './views/student/apply/apply'
import cptdetails from './views/student/cptdetails/cptdetails'

import personal from './views/student/personal/personal'
import personalinfo from './views/student/personal/personalinfo'
import myapplylist from './views/student/personal/myapplylist'
import mywinninglist from './views/student/personal/mywinninglist'
import myexam from './views/student/personal/myexam'

//超级管理员
import adminMain from './views/teacher/main/adminMain'
import details from './views/teacher/main/details'
import collegeadd from './views/teacher/college/add'
import collegelist from './views/teacher/college/list'
import teacheradd from './views/teacher/teacher/add'
import teacherlist from './views/teacher/teacher/list'
import classadd from './views/teacher/class/add'

//教师
import scinfoadd from './views/teacher/scinfo/add'
import scinfolist from './views/teacher/scinfo/list'
import scApplylist from './views/teacher/scinfo/scApplylist'
import sctripadd from './views/teacher/scinfo/sctripadd'

import wiadd from './views/teacher/wi/add'
import wilist from './views/teacher/wi/list'

import studentlist from './views/teacher/studentclass/studentlist'

Vue.use(Router)

let router=new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path:'/',
      name:'index',
      component:index,
      redirect:'/cptlist',
      meta:{title:"首页"},
      children:[
          {
            path:'cptlist',
            name:'cptlist',
            component:cptlist,
            meta:{title:"首页"},
          },
          {
            path:'collegecptlist/:obj?',
            name:'collegecptlist',
            component:collegecptlist,
            meta:{title:"竞赛分类"},

          },
          {
            path:'winninginfolist',
            name:'winninginfolist',
            component:winninginfolist,
            meta:{title:"获奖信息列表"},
          },
          {
            path:'winninginfodetails/:wi_id',
            name:'winninginfodetails',
            component:winninginfodetails,
            meta:{title:"获奖信息详情"},
          },
          {
            path:'/personal',
            name:'personal',
            component:personal,
            redirect:'/personal/personalinfo',
            children:[
              {
                path:'personalinfo',
                name:'personalinfo',
                component:personalinfo,
                meta:{title:'个人信息',requireAuth:true }
              },
              {
                path:'myapplylist',
                name:'myapplylist',
                component:myapplylist,
                meta:{title:'我申请的技能竞赛',requireAuth:true }
              },
              {
                path:'mywinninglist',
                name:'mywinninglist',
                component:mywinninglist,
                meta:{title:'我的获奖信息',requireAuth:true }
              },
              {
                path:'myexam',
                name:'myexam',
                component:myexam,
                meta:{title:'我的获奖信息',requireAuth:true }
              }
            ]
          },
          {
            path:'apply/:obj?',
            name:'apply',
            component:apply,
            meta:{title:'申请技能竞赛',requireAuth:true }//表示进入该路由需要验证身份
          },
          {
            path:'cptdetails/:sc_id',
            name:'cptdetails',
            component:cptdetails,
            meta:{title:'竞赛详情'}
          }
      ]
    },
    {
      path:'/index',
      name:'index2',
      redirect:'cptlist',
      meta:{title:"首页"},
    },
    {
      path:'/login',
      name:'login',
      component:login,
      meta:{title:'登入界面'}
    },
    {
      path:'/adminMain',
      name:'adminMain',
      component:adminMain,
      meta:{title:'技能竞赛管理后台',requireAuth:true },
      redirect:{name:'details'},
      children:[
        {
          path:'details',
          name:'details',
          component:details,
          meta:{title:'系统说明',requireAuth:true }
        },
        {
          path:'scinfoadd/:id?',
          name:'scinfoadd',
          component:scinfoadd,
          meta:{title:'添加技能竞赛信息',requireAuth:true }
          },
          {
            path:'sctripadd/:id?',
            name:'sctripadd',
            component:sctripadd,
            meta:{title:'添加竞赛行程信息',requireAuth:true }
          },
          {
            path:'scinfolist',
            name:'scinfolist',
            component:scinfolist,
            meta:{title:'技能竞赛列表',requireAuth:true }
        },{
            path:'scApplylist',
            name:'scApplylist',
            component:scApplylist,
            meta:{title:'技能竞赛申请列表',requireAuth:true }
        },
        {
          path:'wiadd/:id?',
          name:'wiadd',
          component:wiadd,
          meta:{title:'添加获奖信息',requireAuth:true }
          },
          {
            path:'wilist',
            name:'wilist',
            component:wilist,
            meta:{title:'获奖信息列表',requireAuth:true }
        },
        {
          path:'studentlist',
          name:'studentlist',
          component:studentlist,
          meta:{title:'班级学生列表',requireAuth:true }
        },
        //超级管理员的
        {
        path:'collegeadd/:id?',
        name:'collegeadd',
        component:collegeadd,
        meta:{title:'添加学院信息',requireAuth:true,role:'admin'}
        },
        {
          path:'collegelist',
          name:'collegelist',
          component:collegelist,
          meta:{title:'学院信息列表',requireAuth:true,role:'admin'}
        },
        {
        path:'teacheradd/:id?',
        name:'teacheradd',
        component:teacheradd,
        meta:{title:'添加老师信息',requireAuth:true,role:'admin'}
        },
        {
          path:'teacherlist',
          name:'teacherlist',
          component:teacherlist,
          meta:{title:'教师信息列表',requireAuth:true,role:'admin'}
        },
        {
        path:'classadd/:id?',
        name:'classadd',
        component:classadd,
        meta:{title:'添加班级信息',requireAuth:true,role:'admin'}
        },
        // {
        //   path:'classlist',
        //   name:'classlist',
        //   component:classlist,
        //   meta:{title:'班级信息列表'}
        // },
      ]
    },
    {
      path:'/404',
      name:'404',
      component:page404,
      meta:{title:'404'}
    },
  ]
})

router.beforeEach((to, from, next) => {
  if(to.matched.length===0){
    next('/404')
  }
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
    //验证学生
    if(to.meta.requireAuth && to.path.indexOf("/adminMain")==-1){
      // console.log(to.path,"需要验证");
      if(cookie.get("student")&&cookie.get("cpt-token")){
          // console.log("已通过验证");
          next();
      }else{
        // console.log("未通过验证");
        ElementUI.Message({
          message: '无权限访问,请先登入!',
          type: 'warning'
      });
        next({
            path:'/login',
            query: { redirect: to.fullPath }
        })
      }
      //验证教师
    }else if(to.meta.requireAuth&&to.path.indexOf("/adminMain")!=-1){
        if(cookie.get("teacher")&&cookie.get("cpt-token")&&cookie.get("isAdmin")){
          if(to.meta.role){
              if(cookie.get("isAdmin")!=1){
                ElementUI.Message({
                  message: '非系统超级管理员,无权限访问!',
                  type: 'error',
                });
              }else{
                next()
              }
          }else{
             next();
          }
        }else{
          ElementUI.Message({
            message: '非系统管理员,无权限访问!',
            type: 'warning',
          });
          next({
            path:'/login',
            query: { redirect: to.fullPath }
          })
        }
    }else{
      // console.log(to.path,"不需要验证");
      next()
    }
})
export default router
