<template>
<div>
          <el-card shadow="always" class="elcard">
              <div style="margin-bottom:20px">
                <span style="margin-right:10px">技能竞赛:</span>

                <el-select v-model="scId" @change="change" placeholder="请选择技能竞赛" style="margin-right:10px" :disabled="isUpdate&&isUpdateIn">
                  <el-option
                    v-for="item in scInfoList"
                    :key="item.id"
                    :label="item.scName"
                    :value="item.id">
                  </el-option>
                </el-select>

                <el-button type="primary" round @click="addStep">继续添加竞赛行程</el-button>
                <el-button type="danger" round v-if="stepList.length>2" @click="deleteStep">移除一个竞赛行程</el-button>
              </div>

              <div v-if="scId">
                  <el-steps :active="activeIndex" finish-status="success"  align-center>
                    <el-step  v-for="item in stepList" :key="item.key" :title="item.title">
                    </el-step>
                  </el-steps>

                  <div class="trip-main">
                    <el-form ref="form" :model="scTrip" label-width="120px">
                      <el-form-item label="行程序号">
                        <el-input v-model="scTrip.tripIndex" :disabled="true"></el-input>
                      </el-form-item>

                      <el-form-item label="行程描述">
                        <el-input type="textarea" v-model="scTrip.description"></el-input>
                      </el-form-item>
                      
                      <el-form-item label="行程开始时间:">
                          <el-date-picker type="date" placeholder="选择开始日期" value-format="yyyy-MM-dd HH:mm:ss" v-model="scTrip.startTime" style="width: 100%;"></el-date-picker>
                      </el-form-item>
                       <el-form-item label="行程结束时间:">
                          <el-date-picker type="date" placeholder="选择结束日期" value-format="yyyy-MM-dd HH:mm:ss" v-model="scTrip.endTime" style="width: 100%;"></el-date-picker>
                      </el-form-item>

                      <el-form-item>
                         <el-button type="primary" round @click="selectPre" v-if="activeIndex>1">上一步</el-button>
                         <el-button type="primary" round @click="selectNext" v-if="activeIndex<stepList.length">下一步</el-button>
                         <el-button :type="isUpdate?'success':'primary'"  plain  @click="addScTrip" v-if="activeIndex==scTripList.length">{{isUpdate?'确定修改':'确定添加'}}</el-button>
                      </el-form-item>
                    </el-form>

                  </div>
              </div>


          </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
import  cookie from 'js-cookie'
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
  isUpdate:false,
  isUpdateIn:false,
  teacher:{},
  scId:"",
  scInfoList:[],
  activeIndex:1,
  //最少2个行程 最多5个行程
  stepList:[
          {id:1,title:'行程1'},
          {id:2,title:'行程2'}
  ],
  scTripList:[
          {id:-1,scId:-1,tripIndex:1,description:'',startTime:'',endTime:''},
          {id:-1,scId:-1,tripIndex:2,description:'',startTime:'',endTime:''}
  ],
  scTrip:{
  },
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {
    '$route' (to, from) {
      // 对路由变化作出响应...
      if(to.path!=from.path){
            Object.assign(this.$data, this.$options.data())
      }
      this.scTrip=this.scTripList[this.activeIndex-1]
      if(cookie.get("teacher")){
          let teacherInfo=cookie.get("teacher");
          if(teacherInfo){
            this.teacher = JSON.parse(teacherInfo)
            this.getScInfoList();
          } 
      }
      this.getScInfoList()
      },
  "activeIndex":function(){
    this.scTrip=this.scTripList[this.activeIndex-1]
  },
  "scId":function(){
    for(let i=0;i<this.scTripList.length;i++){
      this.scTripList[i].scId=this.scId;
    }
  }
},
//方法
methods: {
  change(){
    this.activeIndex=1
    this.getScTripList(this.scId)
    this.scTrip=this.scTripList[this.activeIndex-1]
  },
    addScTrip(){
    if(this.scTrip.description && this.scTrip.startTime && this.scTrip.endTime){
      if(!this.isUpdate){
        request({
          url:'/scTrip/add',
          method:'post',
          data:this.scTripList
        }).then(res=>{
          if(res.code!=500){
             this.$message({
                message: '添加竞赛行程成功!!!',
                type: 'success'
            });
          }else{
               this.$message({
                message: '添加竞赛行程出现错误!!!',
                type: 'error'
            });
          }
        }).catch(err=>{
          console.log(err);
        })
      }else{
        request({
          url:'/scTrip/update',
          method:'put',
          data:this.scTripList
        }).then(res=>{
          if(res.code!=500){
             this.$message({
                message: '修改竞赛行程成功!!!',
                type: 'success'
            });
          }else{
               this.$message({
                message: '修改竞赛行程出现错误!!!',
                type: 'error'
            });
          }
        }).catch(err=>{
          console.log(err);
        })
      }

    }else{
      this.$message({
          message: '请正确填该条行程信息!!!',
          type: 'error'
        });
    }
  },
  getScTripList(id){
      request({
        url:`/scTrip/get/${id}`,
        method:'get'
      }).then(res=>{
          this.scTripList=res.data.scTripList;
          if(this.scTripList.length==0){
            if(!this.isUpdateIn){
              this.isUpdate=false;
            }
              this.stepList=[],
              this.scTripList=[];
              this.stepList.push({id:1,title:'行程1'})
              this.stepList.push({id:2,title:'行程2'})
              this.scTripList.push({id:-1,scId:-1,tripIndex:1,description:'',startTime:'',endTime:''})
              this.scTripList.push({id:-1,scId:-1,tripIndex:2,description:'',startTime:'',endTime:''})
              this.scTrip={id:-1,scId:-1,tripIndex:1,description:'',startTime:'',endTime:''}
          }else{
            this.isUpdate=true;
              this.updatedefaultDATA();
          }
      }).catch(err=>{
        console.log(err);
      })
  },
  updatedefaultDATA(){
      this.stepList=[];
      for(let i=0;i<this.scTripList.length;i++){
          this.stepList.push({id:i+1,title:'行程'+(i+1)})
      }
      this.scTrip=this.scTripList[0]
      
  },
  getScInfoList(){
    request({
      url:`/scInfo/teacherScInfoList/${this.teacher.id}`
    }).then(res=>{
        this.scInfoList=res.data.teacherScInfoList
    }).catch(err=>{

    })
  },
  addStep(){
     if(this.stepList.length>4){
        this.$message({
          message: '最多只能为竞赛添加5个行程!!!',
          type: 'warning'
        });
      }else{
          this.stepList.push({id:this.stepList.length+1,title:'行程'+(this.stepList.length+1)})
          this.scTripList.push({id:-1,scId:this.scId,tripIndex:this.scTripList.length+1,description:'',startTime:'',endTime:''})
      }
  },
  deleteStep(){
      if(this.activeIndex==this.stepList.length){
                this.activeIndex=this.activeIndex-1;
      }
      this.stepList.pop();
      this.scTripList.pop();
    
  },
  selectPre(){
    this.activeIndex=this.activeIndex-1;
   
  },
  selectNext(){
    if(this.scTrip.description && this.scTrip.startTime && this.scTrip.endTime){
            this.activeIndex=this.activeIndex+1;
    }else{
      this.$message({
          message: '请正确填该条行程信息!!!',
          type: 'error'
        });
    }
  },

 
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
    this.scTrip=this.scTripList[this.activeIndex-1]

   if(cookie.get("teacher")){
      let teacherInfo=cookie.get("teacher");
      if(teacherInfo){
        this.teacher = JSON.parse(teacherInfo)
        this.getScInfoList();
      } 
  }

  if(this.$route.params&&this.$route.params.id){
      this.isUpdate=true;
      this.isUpdateIn=true;
      this.scId=parseInt(this.$route.params.id)
      this.getScTripList(this.scId);
  }


},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
.trip-main{
  width: 80%;
  padding: 30px;
  background-color: #D5D5D5;
  border-radius: 20px;
}
</style>