<template>
<div class="login-container">
    
    <div class="login-main-container">
        <div class="logo">
             <b>技能竞赛系统</b>
        </div>
        <div class="form-main">
        <el-form :model="loginForm" :rules="rules" ref="ruleForm"  class="login-form" >
            <el-form-item prop="key">
                <el-input prefix-icon="iconfont icon-yonghu" v-model="loginForm.key" placeholder="手机号" autofocus></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input prefix-icon="iconfont icon-mima" v-model="loginForm.password" placeholder="密码" show-password></el-input>
            </el-form-item>
            <el-form-item>
                <el-radio-group v-model="loginForm.type">
                <el-radio label="学生"></el-radio>
                <el-radio label="教师"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')">登入</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
                  <el-button type="success" @click="showDialog">注册</el-button>
            </el-form-item>
        </el-form>
        </div>
    </div>

    <el-dialog title="学生注册" :visible.sync="showStudentRegisterDialog" modal :close-on-click-modal="false" @close="dialogclose">
             <el-form  :model="studentInfo" :rules="studentRules" ref="registerForm"  label-width="100px">
                <el-form-item label="头像：" prop="headerUrl">
                    <el-avatar :src="studentInfo.headerUrl" style="height:80px;width:80px;vertical-align:middle;margin-right:10px;"></el-avatar>
                    <el-upload
                      style="display:inline"
                      name="resources"
                      action="http://localhost:9000/oss/upload"
                      :on-success="uploadFileSuccess"
                      :on-error="uploadFileError"
                      :on-remove="handleRemove"
                      multiple
                      accept=".jpg,.png"
                      :file-list="fileList"
                      >
                      <el-button size="small" type="primary">上传头像</el-button>
                      <div slot="tip" class="el-upload__tip" style="display:inline">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                </el-form-item>

                <el-form-item label="姓名：" prop="name">
                    <el-input v-model="studentInfo.name"></el-input>
                </el-form-item>
                <el-form-item label="性别：" prop="sex">
                    <el-radio v-model="studentInfo.sex" label="男">男</el-radio>
                    <el-radio v-model="studentInfo.sex" label="女">女</el-radio>
                </el-form-item>
                <el-form-item label="手机号：" prop="phone">
                    <el-input v-model="studentInfo.phone" @input="phoneChange"></el-input>
                </el-form-item>
                <el-form-item label="验证码：">
                    <el-input v-model="phoneCode" style="max-width:500px"></el-input>
                    <el-button @click="sendPhoneCode" :disabled="!flag">{{phoneCodebtnText}}</el-button>
                </el-form-item>
                <el-form-item label="密码："  prop="password">
                    <el-input v-model="studentInfo.password"></el-input>
                </el-form-item>
                <el-form-item label="身份证号：" prop="cdCard">
                    <el-input v-model="studentInfo.cdCard"></el-input>
                </el-form-item>
                <el-form-item label="qq：" prop="qq">
                    <el-input v-model="studentInfo.qq"></el-input>
                </el-form-item>
               
                <el-form-item label="班级：" prop="classId">
                    <el-select v-model="studentInfo.collegeId" @change="collegeChange">
                        <el-option :label="item.collegeName" :value="item.id" v-for="item in collegeList" :key="item.id" ></el-option>
                    </el-select>

                    <el-select v-model="studentInfo.classId">
                        <el-option :label="item.className" :value="item.id" v-for="item in classList" :key="item.id"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="register('registerForm')">确定注册</el-button>
                </el-form-item>
             </el-form>
    </el-dialog>

</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
return{
    loginForm: {
          key: '13142211138',
          password: '11111111',
          type:'学生',
    },
    rules: {
          key: [
            { required: true, message: '请输入登入账号', trigger: 'blur' },
            { min: 11, max: 11, message: '请输入正确的手机号', trigger: 'blur' }
          ],
          password:[
            { required: true, message: '请输入登入密码', trigger: 'blur' },
            { min: 8, max: 16, message: '长度在 8 到 16 个字符', trigger: 'blur' }
          ]
    },
    showStudentRegisterDialog:false,
    studentInfo:{
      name:'',
      sex:'男',
      phone:'',
      password:'',
      cdCard:'',
      qq:'',
      headerUrl:'',
      collegeId:'',
      classId:'',
      status:1
    },
    phoneCode:'',//用户输入的验证码
    phoneCodebtnText:'发送验证码',
    flag:false,//true表示发送验证码按钮可用
    studentRules: {
          headerUrl: [
            { required: true, message: '请上传头像', trigger: 'change' },
          ],
          name: [
            { required: true, message: '请填写学生姓名', trigger: 'blur' },
            { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' }
          ],
          sex: [
            { required: true, message: '请选择性别', trigger: 'blur' }
          ],
          phone: [
            { required: true, message: '请填写手机号', trigger: 'blur' },
            { min: 11, max: 11, message: '手机号为11个字符', trigger: 'blur' }
          ],
          password:[
            {required: true, message: '请填写登入密码', trigger: 'blur' },
            { min: 8, max: 16, message: '密码为为8到16个字符', trigger: 'blur' }
          ],
          cdCard:[
            {required: true, message: '请填写身份证号', trigger: 'blur' },
            { min: 8, max: 16, message: '密码为为16到20个字符', trigger: 'blur' }
          ],
          qq: [
            {  required: true, message: '请填写QQ', trigger: 'blur' },
            { min: 5, max: 14, message: 'QQ为为5到14个字符', trigger: 'blur' }
          ],
          classId: [
            {  required: true, message: '请选择班级', trigger: 'change' },
          ]
        },
    fileList:[],
    addInfoSuccess:false,
    collegeList:[],
    classList:[]
};
},
//计算属性
computed: {
},
//监控data中数据变化
watch: {
  'loginForm.type'(){
    if(this.loginForm.type==="学生"){
        this.loginForm.key='13142211138';
        this.loginForm.password='11111111';
    }else if(this.loginForm.type==="教师"){
         this.loginForm.key='15874245519';
        this.loginForm.password='11111111';
    }
  }
},
//方法
methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if(valid){
          if(this.loginForm.type==='学生'){

              request({
                url:'/login/student',
                method:'post',
                data:this.loginForm
              }).then(res=>{
                if(res.code!=500){
                  this.$message({
                    message:res.msg,
                    type:'success'
                  })
                  console.log(res.data);
                  cookie.set("student",res.data.student)
                  cookie.set("cpt-token",res.data.token);
                  if(this.$route.query &&this.$route.query.redirect){
                   window.location.href = this.$route.query.redirect;
                  }else{
                   window.location.href = "/";
                  }
                }else{
                  this.$message({
                    message:res.msg,
                    type:'error'
                  })
                }
              }).catch(err=>{
                console.log(err)
              })

          }else{

              request({
                url:'/login/teacher',
                method:'post',
                data:this.loginForm,
              }).then(res=>{
                  this.$message({
                    message:res.msg,
                    type:'success'
                  })
                  console.log(res.data);
                  cookie.set("teacher",res.data.teacher)
                  cookie.set("cpt-token",res.data.token);
                  cookie.set("isAdmin",res.data.teacher.isAdmin)
                  if(this.$route.query &&this.$route.query.redirect){
                    window.location.href = this.$route.query.redirect;
                  }else{
                    window.location.href = "/adminMain";
                  }
              }).catch(err=>{
                console.log(err)
              })
          }

        }else{
          console.log("err");
          return false;
        }
      });
    },
    resetForm(formName){
        this.$refs[formName].resetFields();
    },
    showDialog(){
      this.showStudentRegisterDialog=true;
      this.getCollegeAndClassList();
    },
    async uploadFileSuccess(response, file, fileList){
      if(this.fileList && this.fileList.length!=0){  //如果之前存在以上传的头像 先进行删除
      let res=  await request({
          url:'/oss/delete',
          method:'delete',
          params:{
              ObjectName:this.fileList[0].ObjectName
          }
      })
       if(res.code!=500){
          this.studentInfo.headerUrl='';
          this.fileList.shift()
        }
      }
      let name=response.data.urlList[0].fileName;//原文件名
      let ObjectName=response.data.urlList[0].resourceName;//oss中的文件名
      let url=response.data.urlList[0].resourceUrl;//oss访问路径
      this.fileList.push({url,name,ObjectName})
      this.studentInfo.headerUrl=url;
    },
    uploadFileError(err, file, fileList){
        this.$refs.upload.clearFiles(); //去掉文件列表
        this.$message({  //弹窗
          type: 'error',
          message: '导上传头像失败！',
          center: true,
          duration: 1000
         });
    },
    handleRemove(file, fileList) {
      //删阿里云中的文件
      request({
        url:'/oss/delete',
        method:'delete',
        params:{
            ObjectName:file.ObjectName
        }
      }).then(res=>{
      }).catch(err=>{
      })
      this.fileList.pop();
      this.studentInfo.headerUrl='';
    },
    dialogclose(){
      if(this.fileList.length!=0&&!this.addInfoSuccess){
        request({
          url:'/oss/delete',
          method:'delete',
          params:{
              ObjectName:this.fileList[0].ObjectName
          }
      }).then(res=>{}).catch(err=>{})
      }
      this.fileList=[];
      this.collegeList=[];
      this.classList=[];
      this.addInfoSuccess=false;
      this.studentInfo={
        name:'',
        sex:'男',
        phone:'',
        password:'',
        cdCard:'',
        qq:'',
        headerUrl:'',
        collegeId:'',
        classId:'',
        status:1
      };
      this.studentPhoneCode='',
      this.phoneCode='',
      this.phoneCodebtnText="发送验证码",
      this.flag=false,
      clearInterval(this.timer)
      this.$refs['registerForm'].resetFields();
    },
    getCollegeAndClassList(){
        //拿学院以及旗下班级 列表
        request({
            url:`/college/collegeList`,
            method:'get',
        }).then(res=>{
            this.collegeList=res.data.collegeList
            this.refreshClassList()
        }).catch(err=>{
            console.log(err)
        })
    },
    collegeChange(v){
        this.refreshClassList();
        this.studentInfo.classId=this.classList[0].id
    },
    refreshClassList(){
          for(let i=0;i<this.collegeList.length;i++){
            if(this.collegeList[i].id==this.studentInfo.collegeId){
                  this.classList=this.collegeList[i].classList;
                break;
            }
        }
    },
    register(formName){
       this.$refs[formName].validate((valid) => {
        if(valid){
            request({
              url:'/student/add',
              method:'post',
              data:this.studentInfo,
              params:{code:this.phoneCode}
            }).then(res=>{
              if(res.code!=500){
                this.$message({
                  message: res.msg,
                  type: 'success'
                });
                this.addInfoSuccess=true;
                this.showStudentRegisterDialog=false;
              }else{
                this.$message({
                  message: res.msg,
                  type: 'error'
                });
              }
            }).catch(err=>{
            })
      }else{
        return false;
      }})
    },
    phoneChange(){
        if(this.studentInfo.phone && this.studentInfo.phone.length==11){
          this.flag=true;
        }else{
          this.flag=false;
        }
    },
    sendPhoneCode(){
          if(!this.flag){
            return;
          }
          request({
                url:`/sms/send/${this.studentInfo.phone}`,
                method:'get'
          }).then(res=>{
              this.studentPhoneCode=res.data.code;
          }).catch(err=>{
              console.log(err);
          })

          this.flag=false;
          let tiemcount=60;
          this.timer = setInterval(()=>{
            this.phoneCodebtnText=tiemcount+"s";
            if(tiemcount<1){
              this.flag = true
              this.phoneCodebtnText='发送验证码'
              clearInterval(this.timer)
            }else{
              tiemcount--;
            }
          },1000)
    },
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
}
};
</script>

<style scoped>
.login-container{
    position: relative;
    height: 100%;
    /* width: 100%; */
    /* height: 100%; */
    background-color: #d2d6de;
}
.login-main-container{
  width: 360px;
  position: absolute;
  left:50%;
  top:50%;
  transform: translate(-50%, -80%);
}
.logo{
    font-size: 35px;
    text-align: center;
    font-weight: 300;
    background-color: #d2d6de;
    box-sizing: border-box;
    padding: 20px;
}
.form-main{
  background: white;
  padding: 40px;
  border-radius: 20px;
}
</style>