<template>
<div>
    <el-card shadow="always" class="elcard">
      <div>{{title}}</div>
      <el-form ref="form" :model="winningInfo" :rules="rules" label-width="120px" label-position="right" style="width:600px">
        <el-form-item label="技能竞赛:" prop="scId">
          <el-select v-model="winningInfo.scId" placeholder="请选择获奖竞赛" @change="selectedChange">
            <el-option  v-for="item in teacherScInfoList" :key="item.id" :label="item.scName" :value="item.id"></el-option>
          </el-select>
        </el-form-item>

      <el-form-item label="参赛获奖人员:" prop="studentId">
          <el-select v-model="winningInfo.studentId" placeholder="请选择获奖人员">
            <el-option  v-for="item in studentList" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="指导老师:" prop="teacherId">
            <span>{{teacher.name}}</span>
            <img :src="teacher.headerUrl" class="header-img">
        </el-form-item>

        <el-form-item label="获奖名称:" prop="wiName">
          <el-input v-model="winningInfo.wiName"></el-input>
        </el-form-item>

        <el-form-item label="获奖描述:" prop="description">
          <el-input v-model="winningInfo.description"></el-input>
        </el-form-item>


        <el-form-item label="获奖人员图片:" prop="winnerImg">
              <el-upload
              name="resources"
              action="http://localhost:9000/oss/upload"
              :limit="1"
              list-type="picture-card"
              :on-exceed="onExceed1"
              :on-success="uploadWinnerImgSuccess"
              :file-list="fileList1"
              >
                <i slot="default" class="el-icon-plus"></i>
                <div slot="file" slot-scope="{file}">
                  <img class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                  <span class="el-upload-list__item-actions">
                    <span class="el-upload-list__item-preview" @click="handleWinnerImgPreview(file)">
                      <i class="el-icon-zoom-in"></i>
                    </span>
                    <span v-if="!disabled1" class="el-upload-list__item-delete"  @click="handleRemoveWinnerImg(file)">
                      <i class="el-icon-delete"></i>
                    </span>
                  </span>
                </div>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible1">
              <img width="100%" :src="dialogImageUrl1" alt="">
            </el-dialog>
        </el-form-item>

        <el-form-item label="获奖证书图片:" prop="certificateImg">
          <el-upload
              name="resources"
              action="http://localhost:9000/oss/upload"
              :limit="1"
              list-type="picture-card"
              :on-exceed="onExceed2"
              :on-success="uploadCertificateImgSuccess"
              :file-list="fileList2"
              >
                <i slot="default" class="el-icon-plus"></i>
                <div slot="file" slot-scope="{file}">
                  <img class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                  <span class="el-upload-list__item-actions">
                    <span class="el-upload-list__item-preview" @click="handleCertificateImgPreview(file)">
                      <i class="el-icon-zoom-in"></i>
                    </span>
                    <span v-if="!disabled2" class="el-upload-list__item-delete"  @click="handleRemoveCertificateImg(file)">
                      <i class="el-icon-delete"></i>
                    </span>
                  </span>
                </div>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible2">
              <img width="100%" :src="dialogImageUrl2" alt="">
            </el-dialog>
        </el-form-item>

        <el-form-item label="获奖相关文件:" prop="winningFile">
          <el-upload
                ref="upload"
                name="resources"
                action="http://localhost:9000/oss/upload"
                multiple
                :on-remove="handleRemove"
                :on-success="uploadFileSuccess"
                :file-list="fileList"
                :limit="1"
                :auto-upload="false">
                <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
                <div slot="tip" class="el-upload__tip">文件总大小不超过20M</div>
                </el-upload>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" v-if="!isUpdate" @click="onSubmit('form')">立即创建</el-button>
          <el-button type="success" v-else  @click="onSubmit('form')">确定修改</el-button>
          <el-button @click="resetForm('form')">取消</el-button>
        </el-form-item>
      </el-form>
  </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
import  cookie from 'js-cookie'
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    title:'添加竞赛获奖信息',
    isUpdate:false,
    isSuccessSubmit:false,

    dialogImageUrl1: '',
    dialogVisible1: false,
    disabled1: false,
    fileList1:[],

    dialogImageUrl2: '',
    dialogVisible2: false,
    disabled2: false,
    fileList2:[],

    fileList:[],
  teacher:{
        // id: 1,
        // name: "谢丁峰",
        // sex: "男",
        // phone: "11111111111",
        // password: "666666",
        // qq: "1954418428",
        // headerUrl: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        // officeName: "二教2101",
        // isAdmin: 1,
        // collegeId: 2,
        // college: null,
        // scInfoList: [],
        // aclass: null
    },
  teacherScInfoList:[],
  studentList:[],
  winningInfo:{
    scId:'',
    studentId:'',
    teacherId:0,
    wiName:"fzy",
    description:'111',
    winnerImg:'',
    certificateImg:'',
    winningFile:'',
  },
  rules: {
    scId: [
      { required: true, message: '请选择技能竞赛', trigger: 'blur' },
    ],
    studentId: [
      { required: true, message: '请选择获奖人员', trigger: 'blur' },
    ],
    teacherId: [
      { required: true, message: '请选择指导老师', trigger: 'change' }
    ],
    wiName: [
      { required: true, message: '请填写获奖名称', trigger: 'change' },
    ],
    description:[
      {required: true, message: '请填写获奖描述', trigger: 'blur' },
    ],
    winnerImg: [
      {  required: true, message: '请上传获奖人员图片', trigger: 'blur' },
    ],
    certificateImg: [
      { required: true, message: '请上传获奖证书图片', trigger: 'blur' }
    ],
    winningFile: [
      { required: true, message: '请上传获奖相关文件', trigger: 'blur' }
      ]
  }
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {
      '$route' (to, from) {
      // 对路由变化作出响应...
        if(to.path!=from.path){
              Object.assign(this.$data, this.$options.data())
        }
        if(cookie.get("teacher")){
          let teacherInfo=cookie.get("teacher");
          if(teacherInfo){
            this.teacher = JSON.parse(teacherInfo)
            this.winningInfo.teacherId=this.teacher.id
        } 
        }
      }
},
//方法
methods: {
        addWinningInfo(){
            request({
              url:'/winningInfo/add',
              method:'post',
              data:{
                  scId:this.winningInfo.scId,
                  studentId:this.winningInfo.studentId,
                  teacherId:this.winningInfo.teacherId,
                  wiName:this.winningInfo.wiName,
                  description:this.winningInfo.description,
                  winnerImg:this.winningInfo.winnerImg,
                  certificateImg:this.winningInfo.certificateImg,
                  winningFile:this.winningInfo.winningFile,
              }
            }).then(res=>{
                if(res.code!=500){
                      console.log("成功");
                      this.$message({
                      message: '添加获奖信息成功',
                      type: 'success'
                      });
                      this.isSuccessSubmit=true;
                      this.$router.push({name:'wilist'})
                  }else{
                      console.log("失败");
                      this.$message({
                      message: res.msg,
                      type: 'error'
                      });
                  }
            })
      },
      updateWinningInfo(){
                request({
              url:'/winningInfo/update',
              method:'put',
              data:{
                  id:this.winningInfo.id,
                  scId:this.winningInfo.scId,
                  studentId:this.winningInfo.studentId,
                  teacherId:this.winningInfo.teacherId,
                  wiName:this.winningInfo.wiName,
                  description:this.winningInfo.description,
                  winnerImg:this.winningInfo.winnerImg,
                  certificateImg:this.winningInfo.certificateImg,
                  winningFile:this.winningInfo.winningFile,
              }
            }).then(res=>{
                if(res.code!=500){
                      console.log("成功");
                      this.$message({
                      message: res.msg,
                      type: 'success'
                      });
                  }else{
                      console.log("失败");
                      this.$message({
                      message: res.msg,
                      type: 'error'
                      });
                  }
            })
      },
        getTeacherScInfoList(){
            request({
              url:`/scInfo/teacherScInfoList/${this.teacher.id}`,
              method:'get'
            }).then(res=>{
              this.teacherScInfoList=res.data.teacherScInfoList
              if(this.winningInfo.scId){
                  this.selectedChange(this.winningInfo.scId);
              }
            })
        },
        selectedChange(scId){
          for(let i=0;i<this.teacherScInfoList.length;i++){
            if(scId == this.teacherScInfoList[i].id){
              this.studentList=this.teacherScInfoList[i].studentList
            }
          }
        },
        submitUpload() {
          this.$refs.upload.submit();
        },
        uploadFileSuccess(response, file, fileList){
            let fileName=response.data.urlList[0].fileName;//原文件名
            let resourceName=response.data.urlList[0].resourceName;//oss中的文件名
            let resourceUrl=response.data.urlList[0].resourceUrl;//oss访问路径
            let resourceType=response.data.urlList[0].resourceType;//类型
             this.winningInfo.winningFile=resourceUrl;
            this.fileList.push({name:fileName,url:resourceUrl,ObjectName:resourceName})
            },
            handleRemove(file, fileList) {
              let ObjectName=file.ObjectName;
              request({
              url:'/oss/delete',
              method:'delete',
              params:{
                  ObjectName
              }
              }).then(res=>{
                  this.winningInfo.winningFile=''
                  this.fileList=[]
              }).catch(err=>{
              })
            },

            uploadWinnerImgSuccess(response, file, fileList){
              let fileName=response.data.urlList[0].fileName;//原文件名
              let resourceName=response.data.urlList[0].resourceName;//oss中的文件名
              let resourceUrl=response.data.urlList[0].resourceUrl;//oss访问路径
              let resourceType=response.data.urlList[0].resourceType;//类型
              this.winningInfo.winnerImg=resourceUrl;
              this.fileList1.push({name:fileName,url:resourceUrl,ObjectName:resourceName})
            },
            handleRemoveWinnerImg(file) {
              console.log(file);
              let ObjectName=file.ObjectName;
              request({
              url:'/oss/delete',
              method:'delete',
              params:{
                  ObjectName
              }
              }).then(res=>{
                  this.winningInfo.winnerImg=''
                  this.fileList1=[]
              }).catch(err=>{
              })
            },
            handleWinnerImgPreview(file) {
              this.dialogImageUrl1 = file.url;
              this.dialogVisible1 = true;
            },
            onExceed1(files, fileList){
                this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
            },

            uploadCertificateImgSuccess(response, file, fileList){
              let fileName=response.data.urlList[0].fileName;//原文件名
              let resourceName=response.data.urlList[0].resourceName;//oss中的文件名
              let resourceUrl=response.data.urlList[0].resourceUrl;//oss访问路径
              let resourceType=response.data.urlList[0].resourceType;//类型
              this.winningInfo.certificateImg=resourceUrl;
              this.fileList2.push({name:fileName,url:resourceUrl,ObjectName:resourceName})
            },
            handleRemoveCertificateImg(file) {
              console.log(file);
              let ObjectName=file.ObjectName;
              request({
              url:'/oss/delete',
              method:'delete',
              params:{
                  ObjectName
              }
              }).then(res=>{
                  this.winningInfo.certificateImg=''
                  this.fileList2=[]
              }).catch(err=>{
              })
            },
            handleCertificateImgPreview(file) {
              this.dialogImageUrl2 = file.url;
              this.dialogVisible2 = true;
            },
            onExceed2(files, fileList){
                this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
            },

            onSubmit(formName){
            this.$refs[formName].validate((valid) => {
                      if (valid) {
                        if(!this.isUpdate){
                            this.addWinningInfo();
                        }else{
                            this.updateWinningInfo();
                        }
                      } else {
                        return false;
                      }
                    });
            },
            resetForm(formName) {
              this.$refs[formName].resetFields();
            },
            // refreshStudentList(){
            //   for(let i=0;i<this.teacherScInfoList.length;i++){
            //     if(this.winningInfo.scId == this.teacherScInfoList[i].id){
            //     this.studentList=this.teacherScInfoList[i].studentList
            //     }
            //    }
            // },
            getWinningInfoById(id){
              request({
                url:`/winningInfo/get/${id}`,
                method:'get'
              }).then(res=>{
                  this.winningInfo=res.data.winningInfo

                  let winnerImg=this.winningInfo.winnerImg;
                  this.fileList1.push({name:winnerImg.slice(97),url:winnerImg,ObjectName:winnerImg.slice(54)})
                  let certificateImg=this.winningInfo.certificateImg;
                  this.fileList2.push({name:certificateImg.slice(97),url:certificateImg,ObjectName:certificateImg.slice(54)})
                  let winningFile=this.winningInfo.winningFile;
                  this.fileList.push({name:winningFile.slice(97),url:winningFile,ObjectName:winningFile.slice(54)})
              }).catch(err=>{
              })
            }
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
  if(cookie.get("teacher")){
    let teacherInfo=cookie.get("teacher");
    if(teacherInfo){
      this.teacher = JSON.parse(teacherInfo)
      this.winningInfo.teacherId=this.teacher.id
    } 
  }

  this.getTeacherScInfoList();

  if(this.$route.params&&this.$route.params.id){
    this.title="修改竞赛获奖信息"
      this.isUpdate=true;
      this.getWinningInfoById(this.$route.params.id);
  }
    


},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
.header-img{
    margin-left: 10px;
    height: 50px;
    width: 50px;
    border-radius: 25px;
    display: inline;
    vertical-align:middle;
}
</style>