<template>
<div style="min-height:500px">
    <div class="search-item">
        <el-form :inline="true" :model="Searchdata" class="demo-form-inline">
            <el-form-item>
                <el-input v-model="Searchdata.querykey" placeholder="请输入查询的关键字"></el-input>
            </el-form-item>
            <el-form-item>
                <el-date-picker  el-date-picker type="date" placeholder="开赛日期" v-model="Searchdata.startTime" style="width: 100%;"></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-date-picker  el-date-picker type="date" placeholder="结赛日期" v-model="Searchdata.endTime" style="width: 100%;"></el-date-picker>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click="search">查询</el-button>
            </el-form-item>
        </el-form>
    </div>
    
    <div v-if="pagelist.scInfoList.length!=0" style="display:flex;flex-wrap: wrap;justify-content: space-evenly;">
        <div class="sc-item" v-for="item in pagelist.scInfoList" :key="item.id" >
            <div class="left">
                <div class="sc-name">
                    <a @click="lookDetails(item.id)">{{item.scName}}</a>
                </div>
                <div class="sc-description">
                    {{item.description}}
                </div>
                <div class="sc-link">
                    <el-link class="sc-link-a" type="primary" @click="lookDetails(item.id)">查看详情</el-link>
                </div>

                <!-- <div>主办单位:{{item.scSponsor}}</div> -->
                <div>发布老师:{{item.teacher.name}}</div>
                <div>归属学院:{{item.college.collegeName}}</div>
                <div>开赛时间:{{item.startTime.slice(0,10)}}</div>
                <div>结赛时间:{{item.endTime.slice(0,10)}}</div>
            </div>
        
            <div class="right">
                <img
                    :src="item.firstUrl?item.firstUrl:'http://img.tuguaishou.com/ips_templ_preview/w432_q100/95/ae/7a/lg_1680129_1585904883_5e86fcf332be3.jpg?auth_key=2241100800-0-0-4378beb02cacac2e53a7cbd87e5f2d31'"/>
            </div>
        </div>
        
        
     </div>
     <div v-else>
        <div>无查询结果</div>
     </div>
    <div>
      <div class="page-item">
            <el-pagination
            background
            layout="prev, pager, next"
            :total="pagelist.total"
            :current-page="pagelist.currentPage"
            :page-size="12"
            @current-change="currentChange"
            >
            </el-pagination>
        </div>
    </div>
</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../../utils/request"

export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    Searchdata:{
        querykey:"",
        startTime:"",
        endTime:"",
    },
    SearchButtonIsChecked:false,
    pagelist:
        {
        total: 20,//总记录数
        pages: 4,//共多少页
        currentPage:1,//当前页
        scInfoList:
        [
        {
            id:1,
            scName:"技能竞赛名称",
            description:"技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述",
            scSponsor:"湖南工业职业技术学院",
            teacher:{id:1,name:"李小明老师"},
            college:{id:1,collegeName:"信息学院"},
            startTime:'2020-11-10',
            endTime:'2020-11-30'
        }
        ]},
    student:{},
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {

},
//方法
methods: {
        lookDetails(sc_id){
            this.$router.push(`/cptdetails/${sc_id}`)
        },
        getpagelistBypage(currentPage){
            if(!(this.Searchdata.querykey||this.Searchdata.startTime||this.Searchdata.endTime))//三个都没有值 就会走这个
                    this.SearchButtonIsChecked=false;
             
            if(this.SearchButtonIsChecked){
                request({
                    url:'/scInfo/list',
                    params:{
                        currentPage,
                        querykey:this.Searchdata.querykey,
                        startTime:this.Searchdata.startTime,
                        endTime:this.Searchdata.endTime
                    },
                    method:'get'
                }).then(res=>{
                    this.pagelist=res.data
                }).catch(err=>{
                    console.log(err)
                })
            }else{
                   request({
                    url:'/scInfo/list',
                    params:{
                        currentPage
                    },
                    method:'get'
                }).then(res=>{
                    this.pagelist=res.data
                }).catch(err=>{
                    console.log(err)
                })
            }
        },
        currentChange(currentPage){
            this.getpagelistBypage(currentPage)
        },
        search(){
            this.SearchButtonIsChecked=true;
            request({
                    url:'/scInfo/list',
                    params:{
                        currentPage:1,
                        querykey:this.Searchdata.querykey,
                        startTime:this.Searchdata.startTime,
                        endTime:this.Searchdata.endTime
                    },
                    method:'get'
                }).then(res=>{
                    this.pagelist=res.data
                }).catch(err=>{
                    console.log(err)
                })
        }

},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
    this.getpagelistBypage(1);
}
}
</script>

<style scoped>

.search-item form{
    height: 40px;
    margin-top: 10px;
}
.sc-item{
    width: 680px;
    display:flex;
    justify-content: space-evenly;
    min-height:250px;
    margin: 10px 0px;
    border-radius: 20px;
    box-sizing: border-box;
    border: 1px solid;
    padding: 10px;
}
.left{
    width:380px;
    box-sizing: border-box;
}
.sc-name{
    text-align: center;
    color: #666;
    font-size: 24px;
}
.sc-name a:hover{
cursor:pointer;
color: #5E79A2;
}
.sc-description{
    text-indent:2em;
    font-size: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
}

.sc-link{
  text-align: right;
  line-height: 16px;
}
.sc-link-a{
font-size: 16px;
margin-right: 5px;
}

.right{
    width: 300px;
}
.right img{
     width:300px;
     height:328px;
     border-radius: 10px;
}

.page-item{
    text-align: center;
    margin: 10px;
}


</style>