<template>
<div class="detail-main">
    <div class="cpt-detail">
        <div class="left">
            <div class="sc-name">{{scInfo.scName}}</div>
            <div class="sc-description">{{scInfo.description}}</div>
            <div class="info-item apply">
                <span>主办单位:{{scInfo.scSponsor}}</span> 
                <el-button class="apply-button" @click="reply" type="success">申请报名</el-button>
            </div>
            <!-- 遇到过错误 -->
            <div class="info-item">
               <span>发布老师:</span> <el-link  target="_blank" :underline="false" style="font-size:20px">
                         {{scInfo.teacher.name}}
                         <img class="teacher-header" :src="scInfo.teacher.headerUrl"/>
                        </el-link>
            </div>
            <div class="info-item">
               <span>所属学院:</span> <el-link  :underline="false" style="font-size:20px" @click="lookcollegecptlist(scInfo.college.id)">
                         {{scInfo.college.collegeName}}
                        </el-link>
            </div>
            <div class="info-item">开赛时间:{{scInfo.startTime.slice(0,10)}}</div>
            <div class="info-item">结赛时间:{{scInfo.endTime.slice(0,10)}}</div>
            
            <div>
              <el-collapse v-model="activeNames" v-if="scTripList.length">
                  <el-collapse-item title="竞赛相关行程:" name="1">
                  <div v-for="item in scTripList" :key="item.id" class="trip-content">
                    <div style="position: relative">
                        <el-tag type="danger" style="fontsize:20px">行程{{item.tripIndex}}:</el-tag>
                        <div style="display:inline"><span>{{item.description}}</span></div>

                      <div style="position:absolute;right:0;top:0" class="trip-time">
                        <span class="el-icon-pie-chart">开始时间:{{item.startTime.slice(0,10)}}</span>
                        <span>—></span>
                        <span class="el-icon-pie-chart">结束时间:{{item.endTime.slice(0,10)}}</span>
                      </div>
                    </div>
                  </div>
                  </el-collapse-item>
              </el-collapse>

                <span v-else>竞赛相关行程:占无相关行程</span>
            </div>

            <span>相关图片:</span> 
            <div class="img-list" v-if="imgList.length">
                <div v-for="(item,index) in imgList" :key="index">
                    <img :src="item"/>
                </div>
            </div>
            <span v-else>
                占无相关图片
            </span>
        </div>

        <div class="right">
            <span>相关文件列表:</span>
            <div class="file-list" v-if="fileList.length">
                <form :action="item.resourceUrl" class="file-item" v-for="(item,index) in fileList" :key="index">
                    <img style="width:50px;height:50px;" src="https://cptmanagement-fzy.oss-cn-beijing.aliyuncs.com/2020-11-24/%E6%96%87%E4%BB%B6.jpg"/>
                    <div>{{item.fileName}}</div>
                    <intput type="hidden" name="Content-Disposition" :value="'attachment;filename='+item.resourceName"></intput>
                    <button  style="height:30px"  type="primary">下载此文件</button>
                </form>
            </div>
            <div v-else>
                <span>占无相关文件</span>
            </div>
        </div>
        
    </div>
</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    photoVisible: false,
       scInfo:
        {
            // id:1,
            // scName:"技能竞赛名称",
            // description:"技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述技能竞赛描述",
            // scSponsor:"湖南工业职业技术学院",
            // teacher:{id:1,name:"李小明老师",headerUrl:'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg'},
            // college:{id:1,collegeName:"信息学院"},
            // startTime:'2020-11-10',
            // endTime:'2020-11-30',
        },
        imgList:[
            // 'https://cptmanagement-fzy.oss-cn-beijing.aliyuncs.com/2020-11-06/4735f0fa36b74bd7b34f6a90aa9da836QQ.jpg',
            // 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg'
        ],
        fileList:[
                // {fileName:'my.ini',resourceName:'9f6d8324775a4d24b0e81a04d6e1c7e9my.ini',resourceUrl:'https://cptmanagement-fzy.oss-cn-beijing.aliyuncs.com/2020-11-06/9f6d8324775a4d24b0e81a04d6e1c7e9my.ini'},
                // {fileName:'QQ.jpg',resourceName:'4735f0fa36b74bd7b34f6a90aa9da836QQ.jpg',resourceUrl:'https://cptmanagement-fzy.oss-cn-beijing.aliyuncs.com/2020-11-06/4735f0fa36b74bd7b34f6a90aa9da836QQ.jpg'},
        ],
        scTripList:[],
        activeNames:["1"]
    
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {

},
//方法
methods: {
getcptInfo(sc_id){
    request({
        url:`/scInfo/get/${sc_id}`,
        method:'get'
    }).then(res=>{
        console.log("cpt",res)
        this.scInfo=res.data.scInfo;
        this.imgList=res.data.imgList;
        this.fileList=res.data.fileList;
    }).then(err=>{
        console.log(err)
    })
},
getscTrip(sc_id){
    request({
      url:`/scTrip/get/${sc_id}`,
      method:'get'
    }).then(res=>{
      this.scTripList=res.data.scTripList
    }).catch(err=>{
      console.log(err);
    })
},
reply(){
 this.$router.push({
        name: 'apply',
        params: {
           collegeId:this.scInfo.college.id,
           scId:this.scInfo.id,
        }
    })
},
lookcollegecptlist(collegeId){
     this.$router.push({
        name: 'collegecptlist',
        params: {
           collegeId
        }
    })
    
}
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
this.getcptInfo(this.$route.params.sc_id);
this.getscTrip(this.$route.params.sc_id);

}
};
</script>

<style scoped>
.cpt-detail{
    
    min-height: 700px;
    width: 1080px;
    margin: 10px auto 10px auto;
    display: flex;
    justify-content: space-between;
}
.cpt-detail .left{
    width: 880px;
    border: salmon solid 1px;
    border-radius: 10px;
    padding: 10px;
    box-sizing: border-box;
}
.left .sc-name{
    text-align: center;
    font-size: 30px;
    font-weight: 700;
}
.left .sc-description{
    text-indent:2em;
    
    letter-spacing:2px;
    background-color: rgb(164, 211, 250);
}

.left .info-item{
    margin: 10px 0;
}

.apply{
    position: relative;
}
.apply .apply-button{
    position: absolute;
    right: 0;
}

.img-list{
display: flex;
justify-content: space-evenly;
 flex-wrap: wrap;
}

.img-list img{
    height: 450px;
    width: 400px;
    margin: 5px;
    border-radius: 10px;
}

.teacher-header{
    display: inline-block;
    height: 40px;
    width: 40px;
    border-radius: 20px;
    vertical-align:middle
}

.cpt-detail .right{
    width: 180px;
    border: solid 1px springgreen;
    padding: 10px;
    box-sizing: border-box;
    border-radius: 20px;
}
.file-item{
    margin-bottom: 5px;
}

.trip-content{
  font-size: 16px;
  line-height: 32px;
}
</style>