<template>
<div style="min-height:500px;margin-bottom:10px;">
       <el-table
    height="500"
    :data="ScWinningInfo"
    style="width: 100%;margin-bottom:50px;">

        <el-table-column
        label="获奖竞赛名称"
        width="180">
        <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.scName }}</span>
        </template>
        </el-table-column>

        <el-table-column
        label="指导老师"
        width="180">
        <template slot-scope="scope">
            <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.teacher.name }}</el-tag>
            </div>
        </template>
        </el-table-column>

        <el-table-column
        label="获奖名称"
        width="180">
        <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.wiName}}</span>
        </template>
        </el-table-column>

        <el-table-column
        label="获奖信息描述"
        width="200">
        <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.description }}</span>
        </template>
        </el-table-column>


        <el-table-column label="操作">
        <template slot-scope="scope">
            <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">查看详情</el-button>
        </template>
        </el-table-column>
  </el-table>
    
</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
       ScWinningInfo: [
        {
          id:0,
          scName:'技能竞赛名称',
          teacher:{id:1,name:'张三老师'},
          wiName: '获奖名称1111111111111111111',
          description:"获奖描述描述描述",
        }
        ]

};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {

},
//方法
methods: {
        getScWinningInfo(){
        let id=JSON.parse(cookie.get('student')).id;
         if(id){
             request({
                 url:`/winningInfo/studentWinningInfoList/${id}`,
                 method:'get'
             }).
             then(res=>{
                    this.ScWinningInfo=res.data.ScWinningInfo
             }).catch(err=>{
                 console.log(err)
             })
         }
        },
        handleEdit(index, row) {
        console.log(index, row);
        this.$router.push('/winninginfodetails/'+row.id)
      }
      
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
   this.getScWinningInfo();
},

};
</script>

<style scoped>
</style>