<script>
import  cookie from 'js-cookie'
import request from "../../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    defaultActive:this.$route.name,
    teacher:{
        id: 1,
        name: "谢丁峰",
        sex: "男",
        phone: "11111111111",
        password: "666666",
        qq: "1954418428",
        headerUrl: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        officeName: "二教2101",
        isAdmin: 1,
        collegeId: 1,
        college: null,
        scInfoList: [],
        aclass: null
    },
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {
    '$route' (to, from) {
      // 对路由变化作出响应...
      this.defaultActive=to.name;
    }
},
//方法
methods: {
        exit(){
             if(cookie.get('teacher')&&cookie.get('cpt-token')){
                  request({
                    url:'/login/logout',
                    method:'get',
                  }).then(res=>{
                      if(res!=500){
                          //删除cookie
                          cookie.remove('teacher')
                          cookie.remove('cpt-token')
                          cookie.remove('isAdmin')
                          //回到首页面
                          window.location.href = "/";
                      }
                  }).catch(err=>{
                      console.log(err);
                  })
              }
            //回到首页面
            window.location.href = "/";
        },
        getTeacherInfo(){
                  if(cookie.get("teacher")){
                  let teacherInfo=cookie.get("teacher");
                  if(teacherInfo){
                    this.teacher = JSON.parse(teacherInfo)
                  } 
                  }
                  // console.log("cpt-token",cookie.get("cpt-token"));
        },
        // handleOpen(key, keyPath) {
        //     console.log(key, keyPath);
        // },
        // handleClose(key, keyPath) {
        //     console.log(key, keyPath);
        // }
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
  this.getTeacherInfo()
}
};
</script>

