import axios from 'axios'
import {MessageBox,Message} from 'element-ui'
import cookie from 'js-cookie'
// 创建axios实例
const service = axios.create({
  baseURL: 'http://101.43.37.152:9000', // api的base_url
  timeout: 20000 // 请求超时时间
})

//http request 拦截器  每次发Request请求时 都会进行拦截
service.interceptors.request.use(
  config=>{
    // console.log("发送请求前");
    
    if(cookie.get("cpt-token")){//判断cookie中是否有cookie值
      //有的话 就将token值设置到请求头
      config.headers['token']=cookie.get("cpt-token")
    }
    return config;
  },
  err=>{
    return Promise.reject(err);
  }
)

//响应拦截
service.interceptors.response.use(
  response=>{
    // if(response.data.code==28004){
    //   console.log("response.data.code是28004")
    //   //返回错误代码-1 清除ticket信息并跳转到登入界面
    //   //debugger
    //   window.location.href="/login"
    //   return;
    // }else{
    // if(response.data.code!==20000){
    //   //25000 订单支付中 不做任何提示
    //   if(response.data.code!==25000){
    //     Message({
    //       message:response.data.message||'error',
    //       type:'error',
    //       duration:5*1000
    //     })
    //   }
    // }else{
    //   return response;
    // }
    let res=response.data;
    return res;
  }
,err=>{
  return Promise.reject(err);
}
)
export default service