<template>
<div>
        <el-card shadow="always" class="elcard">
            <el-table
              :data="teacherScInfoList"
              style="width: 100%">
              <el-table-column
                label="技能竞赛名称"
                width="180">
                <template slot-scope="scope">
                  <span style="margin-left: 10px">{{ scope.row.scName }}</span>
                </template>
              </el-table-column>

              <el-table-column
                label="主办方单位"
                width="180">
                <template slot-scope="scope">
                  <span style="margin-left: 10px">{{ scope.row.scSponsor }}</span>
                </template>
              </el-table-column>
              
              <el-table-column
                label="竞赛描述"
                width="180">
                <template slot-scope="scope">
                  <span style="margin-left: 10px">{{ scope.row.description }}</span>
                </template>
              </el-table-column>
              
              <el-table-column
                label="开赛时间"
                width="200">
                <template slot-scope="scope">
                  <i class="el-icon-time"></i>
                  <span style="margin-left: 10px">{{ scope.row.startTime }}</span>
                </template>
              </el-table-column>

              <el-table-column
                label="结赛时间"
                width="200">
                <template slot-scope="scope">
                  <i class="el-icon-time"></i>
                  <span style="margin-left: 10px">{{ scope.row.endTime }}</span>
                </template>
              </el-table-column>

              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
        </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
  teacher:{
        id: 1,
        name: "谢丁峰",
        sex: "男",
        phone: "11111111111",
        password: "666666",
        qq: "1954418428",
        headerUrl: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        officeName: "二教2101",
        isAdmin: 1,
        collegeId: 2,
        college: null,
        scInfoList: [],
        aclass: null
    },
  teacherScInfoList:[]
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {

},
//方法
methods: {
  getTeacherScInfoList(){
      request({
        url:`/scInfo/teacherScInfoList/${this.teacher.id}`,
        method:'get'
      }).then(res=>{
        this.teacherScInfoList=res.data.teacherScInfoList
      })
  },
    handleEdit(index, row){
          this.$router.push('/adminMain/scinfoadd/'+row.id)
    },
    handleDelete(index,row){
          this.$confirm('此操作将永久该技能竞赛信息、与其相关的申请信息，获奖信息，资源文件信息等, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
          }).then(() => {
            request({
                url:`/scInfo/delete/${row.id}`,
                method:'delete'
            }).then(res=>{
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
                this.getTeacherScInfoList();
            }).catch(err=>{
                 this.$message({
                    type: 'error',
                    message: '删除失败!'
                });
            })
            })
  }
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
  this.getTeacherScInfoList()
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
</style>