<template>
<div>
    <el-card shadow="always" class="elcard">
            <div>学院列表</div>
            <el-table
            :data="collegeList"
            border
            style="width: 100%">
                <el-table-column
                label="学院名称"
                width="170">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.collegeName }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                label="学院办公室"
                width="180">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.officeName }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                label="操作">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
                </el-table-column>
            </el-table>
    </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    collegeList: [{
        collegeName: '王小虎',
        officeName: '上海市普陀区金沙江路 1518 弄'
    }, {
        collegeName: '王小虎',
        officeName: '上海市普陀区金沙江路 1517 弄'
    }, {
        collegeName: '王小虎',
        officeName: '上海市普陀区金沙江路 1519 弄'
    }]
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {

},
//方法
methods: {
       getCollegeList(){
                request({
                url:'/college/list',
                method:'get'
                }).then(res=>{
                        this.collegeList=res.data.collegeList
                }).catch(err=>{
                })
        },
        handleEdit(index, row) {
            console.log(index, row);
            this.$router.push({
                path:`collegeadd/${row.id}`,
            })
        },
        handleDelete(index, row) {
            console.log(index, row);
            this.$confirm('此操作将永久删除学院信息,以及该学院下的老师、学生、班级、技能竞赛信息等,是否继续?', '危险警告', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'error',
            }).then(() => {
                    request({
                        url:`/college/delete/${row.id}`,
                        method:'delete'
                    }).then(res=>{
                            if(res.code!=500){
                                this.$message({
                                type: 'success',
                                message: '删除成功!'
                                });
                                this.getCollegeList()
                            }else{
                                this.$message({
                                type: 'error',
                                message: '删除失败!'
                                });
                            }
                        }).catch(err=>{console.log(err);})
                        
                }).catch(err=>{
                    this.$message({
                        type: 'warning',
                        message: '取消删除!'
                    });
                })
        }
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
this.getCollegeList()
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {
},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
</style>