<template>
<div>
        <el-card shadow="always" class="elcard">
            <div>添加班级信息</div>
                <el-form :model="classData" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

                <el-form-item label="所属学院:" prop="collegeId">
                        <el-select v-model="classData.collegeId" placeholder="请选择" @change="refreshTeacherList">
                            <el-option
                            v-for="item in collegeList"
                            :key="item.id"
                            :label="item.collegeName"
                            :value="item.id">
                             </el-option>
                        </el-select>
                </el-form-item>
                <el-form-item label="辅导员:" prop="teacherId">
                        <el-select v-model="classData.teacherId" placeholder="请选择">
                            <el-option
                            v-for="item in teacherList"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                             </el-option>
                        </el-select>
                </el-form-item>

                <el-form-item label="班级名称:" prop="className">
                    <el-input v-model="classData.className"></el-input>
                </el-form-item>
                
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')">确定添加</el-button>
                    <el-button @click="resetForm('ruleForm')">重置</el-button>
                </el-form-item>
            </el-form>
        </el-card>

</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
        isUpdate:false,
        collegeList:[],
        teacherList:[],
        classData:{
                className:'',
                collegeId:'',
                teacherId:''
        },
        rules: {
          className: [
            { required: true, message: '请填写教师姓名', trigger: 'blur' },
            { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' }
          ],
          collegeId: [
            { required: true, message: '请选择学院', trigger: 'blur' }
          ],
          teacherId: [
            { required: true, message: '请选择辅导员', trigger: 'blur' }
        ]
        }
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {

},
//方法
methods: {
        addClass(){
                request({
                url:'/class/add',
                method:'post',
                data:{
                        className:this.classData.className,
                        collegeId:this.classData.collegeId,
                        teacherId:this.classData.teacherId,
                }
                }).then(res=>{
                console.log(res);
                if(res.code!=500){
                        console.log("成功");
                        this.$message({
                        message: '添加班级信息成功',
                        type: 'success'
                        });
                }else{
                        console.log("失败");
                        this.$message({
                        message: res.msg,
                        type: 'error'
                        });
                }
                }).catch(err=>{
                console.log(err);
                })
        },
        getCollegeList(){
                request({
                url:'/college/collegeTeacherList',
                method:'get'
                }).then(res=>{
                        this.collegeList=res.data.collegeList
                }).catch(err=>{
                })
        },
        refreshTeacherList(){
                for(let i=0;i<this.collegeList.length;i++){
                if(this.classData.collegeId==this.collegeList[i].id){
                this.teacherList=this.collegeList[i].teacherList;
        }
        }},
        submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.addClass();
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
        

},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
        this.getCollegeList()
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
</style>