<template>
<div>
        <el-card shadow="always" class="elcard">
        <div>{{title}}</div>
        <div style="display:flex;">
            <div style="margin-right:20px">
                <el-form ref="form" :rules="rules" :model="scinfo" label-width="150px" style="width:500px">
                    <el-form-item label="技能竞赛名称" prop="scName">
                        <el-input v-model="scinfo.scName"></el-input>
                    </el-form-item>
                    <el-form-item label="主办方" prop="scSponsor">
                        <el-input v-model="scinfo.scSponsor"></el-input>
                    </el-form-item>
                    <el-form-item label="所属学院" prop="collegeId">
                        <el-select v-model="scinfo.collegeId" placeholder="请选择学院">
                            <el-option v-for="item in collegeList" :key="item.id" :value="item.id" :label="item.collegeName"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="技能竞赛描述" prop="description">
                        <el-input type="textarea" rows="3" v-model="scinfo.description"></el-input>
                    </el-form-item>
                    <el-form-item label="指导老师" prop="teacherId">
                        <span>{{teacher.name}}</span>
                        <img :src="teacher.headerUrl" class="header-img">
                    </el-form-item>
                    <el-form-item label="竞赛开赛时间" prop="startTime">
                        <el-date-picker type="date" placeholder="选择开赛日期" v-model="scinfo.startTime" style="width: 100%;"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="竞赛结赛时间" prop="endTime">
                        <el-date-picker type="date" placeholder="选择结赛日期" v-model="scinfo.endTime" style="width: 100%;"></el-date-picker>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" v-if="!isUpdate" @click="onSubmit('form')">确定添加</el-button>
                        <el-button type="success" v-else  @click="onSubmit('form')">确定修改</el-button>
                        <el-button type="warning" v-if="!isUpdate" @click="resetForm('form')">重置</el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-button  size="small" type="primary" @click="addTrip" :disabled="!scId">为此技能竞赛添加行程</el-button>
                    </el-form-item>
                </el-form>

            </div>
            <div>
                <div>{{scId?'为此技能竞赛信息添加相关文件':'请先添加一个技能竞赛信息,再为此添加相关文件'}}</div>
                <el-upload
                ref="upload"
                name="resources"
                action="http://localhost:9000/oss/upload"
                multiple
                :on-remove="handleRemove"
                :on-success="uploadFileSuccess"
                :file-list="fileList"
                :auto-upload="false">
                <el-button slot="trigger" size="small" type="primary" :disabled="!scId">选取文件</el-button>
                <el-button style="margin-left: 10px;" size="small" type="success" :disabled="!scId" @click="submitUpload">上传到服务器</el-button>
                <div slot="tip" class="el-upload__tip">文件总大小不超过20M</div>
                </el-upload>
            </div>

           
        </div>
        </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
import  cookie from 'js-cookie'
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    title:'添加技能竞赛信息',
    isUpdate:false,
    isSuccessSubmit:false,

    teacher:{
        id: 1,
        name: "谢丁峰",
        sex: "男",
        phone: "11111111111",
        password: "666666",
        qq: "1954418428",
        headerUrl: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        officeName: "二教2101",
        isAdmin: 1,
        collegeId: 2,
        college: null,
        scInfoList: [],
        aclass: null
    },
    scId:0,
    isUpload:false,
    isReSubmit:false,
    scinfo:{
        scName:'',
        scSponsor:'',
        collegeId:1,
        description:'',
        status:1,
        startTime:'',
        endTime:''
    },
    rules: {
          scName: [
            { required: true, message: '请填写技能竞赛名称', trigger: 'blur' },
          ],
          scSponsor: [
            { required: true, message: '请填写主办方名称', trigger: 'blur' },
          ],
          collegeId: [
            { required: true, message: '请选择学院', trigger: 'blur' }
          ],
          description: [
            { required: true, message: '请填写竞赛描述', trigger: 'blur' },
          ],
          startTime: [
            {  required: true, message: '请填写开赛时间', trigger: 'blur' },
          ],
          endTime: [
            { required: true, message: '请填写结赛时间', trigger: 'blur' }
          ]
        },
    collegeList:[],
    scResourceList:[],
    fileList: [
        // {
        //  name: 'food.jpeg',
        //  url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
        // }
    ]
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {
      '$route' (to, from) {
      // 对路由变化作出响应...
      if(to.path!=from.path){
            Object.assign(this.$data, this.$options.data())}
      },
},
//方法
methods: {
    getTeacher(){

    },
    getScInfo(id){
      request({
        url:`/scInfo/get/${id}`,
        method:'get'
      }).then(res=>{
          this.scinfo=res.data.scInfo
          this.scId=this.scinfo.id
          if(this.scinfo.scResourceList.length){
            this.isUpload=true
          }
          for(let i=0;i<this.scinfo.scResourceList.length;i++){
                this.fileList.push(
                  {
                  name:this.scinfo.scResourceList[i].fileName,
                  url:this.scinfo.scResourceList[i].resourceUrl,
                  ObjectName:this.scinfo.scResourceList[i].resourceName,
                  id:this.scinfo.scResourceList[i].id
                  })
          }
      })
    },
    getCollegeList(){
        request({
            url:'/college/list',
            method:'get'
        }).then(res=>{
            this.collegeList=res.data.collegeList;
        })
    },
    formateDate(datetime) {
      // let  = "2019-11-06T16:00:00.000Z"
        function addDateZero(num) {
            return (num < 10 ? "0" + num : num);
        }
        let d = new Date(datetime);
        let formatdatetime = d.getFullYear() + '-' + addDateZero(d.getMonth() + 1) + '-' + addDateZero(d.getDate()) + ' ' + addDateZero(d.getHours()) + ':' + addDateZero(d.getMinutes()) + ':' + addDateZero(d.getSeconds());
        return formatdatetime;
    },
        addScInfo(){
            request({
                url:'/scInfo/add',
                method:'post',
                data:{
                    scName:this.scinfo.scName,
                    scSponsor:this.scinfo.scSponsor,
                    collegeId:this.scinfo.collegeId,
                    description:this.scinfo.description,
                    teacherId:this.teacher.id,
                    status:this.scinfo.status,
                    startTime: this.formateDate(this.scinfo.startTime),
                    endTime: this.formateDate(this.scinfo.endTime)
                }
            }).
            then(res=>{
                if(res.code!=500){
                    this.scId= res.data.scInfo.id;
                    this.isReSubmit=true;
                     this.$message({
                        message: '添加成功',
                        type: 'success'
                    });
                }else{
                     this.$message({
                        message: '添加失败',
                        type: 'error'
                    });
                }
               
            }).catch(err=>{

            })
        },
        updateScInfo(){
              request({
                url:'/scInfo/update',
                method:'put',
                data:{
                    id:this.scinfo.id,
                    scName:this.scinfo.scName,
                    scSponsor:this.scinfo.scSponsor,
                    collegeId:this.scinfo.collegeId,
                    description:this.scinfo.description,
                    teacherId:this.teacher.id,
                    status:this.scinfo.status,
                    startTime: this.formateDate(this.scinfo.startTime),
                    endTime: this.formateDate(this.scinfo.endTime)
                }
            }).
            then(res=>{
                if(res.code!=500){
                     this.$message({
                        message: '修改成功',
                        type: 'success'
                    });
                }else{
                     this.$message({
                        message: '修改失败',
                        type: 'error'
                    });
                }
            }).catch(err=>{
            })
        },
        submitUpload() {
            if(this.scId){
                this.$refs.upload.submit();
            }else{
                this.$message({
                    message: '请先添加技能竞赛信息',
                     type: 'warning'
                })
            }
        },
        async uploadFileSuccess(response, file, fileList){
            console.log("uploadFileSuccess");
            let fileName=response.data.urlList[0].fileName;//原文件名
            let resourceName=response.data.urlList[0].resourceName;//oss中的文件名
            let resourceUrl=response.data.urlList[0].resourceUrl;//oss访问路径
            let resourceType=response.data.urlList[0].resourceType;//类型
            let id=0;
            let res=await request({
                url:'/scResource/add',
                method:'post',
                data:{
                    scId:this.scId,
                    fileName,
                    resourceName,
                    resourceUrl,
                    resourceType,
                }
            })
            if(res.code!=500){
              this.isUpload=true;
                console.log("上传成功后的资源id:",res.data.scResource.id);
                 this.fileList.push(
                {
                name:fileName,
                url:resourceUrl,
                ObjectName:resourceName,
                id:res.data.scResource.id
                })
                this.$message({
                    message: '上传文件成功',
                     type: 'success'
                })
            }
        },
        handleRemove(file, fileList) {
            //删阿里云中的文件
            request({
            url:'/oss/delete',
            method:'delete',
            params:{
                ObjectName:file.ObjectName
            }
            }).then(res=>{
            }).catch(err=>{
            })
            //删数据库中的资源信息
            request({
              url:`/scResource/delete/${file.id}`,
              method:'delete'
            }).then(res=>{
            }).catch(err=>{
            })
            let filter =this.fileList.filter(
               (item) => item.id!= file.id
             );
             this.fileList=filter
             if(this.fileList.length==0){
               this.isUpload=false
             }

        },
        onSubmit(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if(!this.isUpdate){
                if(this.isReSubmit){
                  this.$message({
                    message: '请勿重复提交技能竞赛信息',
                     type: 'warning'
                  })
              }else{
                   this.addScInfo()
              }
            }else{
              this.updateScInfo();
            }
              
             
          } else {
            console.log('error submit!!');
            return false;
          }
        });
        },
         resetForm(formName) {
            this.$refs[formName].resetFields();
            this.isReSubmit=false;
            this.scId=0;
            this.$refs.upload.clearFiles(); 
            this.isUpload=false;
        },
        addTrip(){
          if(this.scId)
            this.$router.push('/adminMain/sctripadd/'+this.scId)
        }
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
    if(cookie.get("teacher")){
      let teacherInfo=cookie.get("teacher");
      if(teacherInfo){
        this.teacher = JSON.parse(teacherInfo)
      } 
    }

  if(this.$route.params&&this.$route.params.id){
      this.title="修改技能竞赛信息"
      this.isUpdate=true;
      this.getScInfo(this.$route.params.id)
      this.isSuccessSubmit=true;
  }

this.getCollegeList()
this.scinfo.collegeId=this.teacher.collegeId;
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {
 
},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
beforeRouteLeave: function(to, from , next){
    next(false)
    if(this.scId && !this.isUpload){
        this.$confirm('您还未给此技能竞赛添加相关文件, 确定要离开?', '提示', {
          distinguishCancelAndClose: true,
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          console.log("确定");
          next()
          this.sure();
          
        }).catch(() => {
        });
     }else{
          next()
     }
    
}
    
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
.header-img{
    margin-left: 10px;
    height: 50px;
    width: 50px;
    border-radius: 25px;
    display: inline;
    vertical-align:middle;
}
</style>