<template>
<div>
        <el-card shadow="always" class="elcard">
            <div>{{title}}</div>
            <el-form :model="teacher" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
                <el-form-item label="头像:" prop="headerUrl">
                    <img :src="teacher.headerUrl" alt="" class="header-img">
                    <el-upload
                    ref="upload"
                    name="resources"
                    action="http://localhost:9000/oss/upload"
                    :on-success="uploadFileSuccess"
                    :on-error="uploadFileError"
                    multiple
                    accept=".jpg,.png"
                    >
                    <el-button size="small" type="primary">上传头像</el-button>
                    <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                </el-form-item>

                <el-form-item label="姓名:" prop="name">
                    <el-input v-model="teacher.name"></el-input>
                </el-form-item>
                <el-form-item label="性别:" prop="sex">
                    <el-radio v-model="teacher.sex" label="男">男</el-radio>
                    <el-radio v-model="teacher.sex" label="女">女</el-radio>
                </el-form-item>
                <el-form-item label="手机:" prop="phone">
                    <el-input v-model="teacher.phone"></el-input>
                </el-form-item>
                <el-form-item label="密码:" prop="password">
                    <el-input v-model="teacher.password"></el-input>
                </el-form-item>
                <el-form-item label="QQ:" prop="qq">
                    <el-input v-model="teacher.qq"></el-input>
                </el-form-item>
                <el-form-item label="办公室名称:" prop="officeName">
                    <el-input v-model="teacher.officeName"></el-input>
                </el-form-item>
                <el-form-item label="是否为超级管理员:" prop="isAdmin">
                        <el-switch
                            v-model="teacher.isAdmin"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            :active-value="1"
                            :inactive-value="0">
                        </el-switch>
                </el-form-item>
                  <el-form-item label="所属学院:" prop="collegeId">
                        <el-select v-model="teacher.collegeId" placeholder="请选择">
                            <el-option
                            v-for="item in collegeList"
                            :key="item.id"
                            :label="item.collegeName"
                            :value="item.id">
                             </el-option>
                        </el-select>
                       
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-if="!isUpdate" @click="submitForm('ruleForm')">确定添加</el-button>
                    <el-button type="success" v-else  @click="submitForm('ruleForm')">确定修改</el-button>
                    <el-button @click="resetForm('ruleForm')">重置</el-button>
                </el-form-item>
            </el-form>
        </el-card>

</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    title:'添加教师信息',
    isUpdate:false,
    isSuccessSubmit:false,
    collegeList:[],
    teacher: {
            headerUrl: 'https://dss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=2561659095,299912888&fm=26&gp=0.jpg',
            name: '范范范',
            sex: '男',
            phone: '13142211138',
            password: '123456789',
            qq: '1954418428',
            officeName: '1教101',
            isAdmin: 0,
            collegeId:''
    },
    ObjectName:'',
    rules: {
          headerUrl: [
            { required: true, message: '请上传头像', trigger: 'blur' },
          ],
          name: [
            { required: true, message: '请填写教师姓名', trigger: 'blur' },
            { min: 2, max: 8, message: '长度在 2 到 8 个字符', trigger: 'blur' }
          ],
          sex: [
            { required: true, message: '请选择性别', trigger: 'change' }
          ],
          phone: [
            { required: true, message: '请填写手机号', trigger: 'change' },
            { min: 11, max: 11, message: '手机号为11个字符', trigger: 'blur' }
          ],
          password:[
            {required: true, message: '请填写登入密码', trigger: 'blur' },
            { min: 8, max: 16, message: '密码为为8到16个字符', trigger: 'blur' }
          ],
          qq: [
            {  required: true, message: '请填写QQ', trigger: 'blur' },
            { min: 5, max: 14, message: 'QQ为为5到14个字符', trigger: 'blur' }
          ],
          officeName: [
            { required: true, message: '请填写办公室地址', trigger: 'blur' }
          ],
          isAdmin: [
            { required: true, message: '请填写是否为超级管理员', trigger: 'blur' }
          ],
          collegeId: [
            { required: true, message: '请选择学院', trigger: 'blur' }
          ]
        }

};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {
'$route' (to, from) {
      // 对路由变化作出响应...
      if(to.path!=from.path){
            Object.assign(this.$data, this.$options.data())}
        }
},
//方法
methods: {
    getCollgeList(){
        request({
            url:'/college/list'
        }).then(res=>{
            this.collegeList=res.data.collegeList    
        })
    },
    addTeacher(){
      request({
        url:'/teacher/add',
        method:'post',
        data:{
            headerUrl: this.teacher.headerUrl,
            name:this.teacher.name,
            sex: this.teacher.sex,
            phone: this.teacher.phone,
            password: this.teacher.password,
            qq: this.teacher.qq,
            officeName: this.teacher.officeName,
            isAdmin: this.teacher.isAdmin,
            collegeId:this.teacher.collegeId,
        }
      }).then(res=>{
         if(res.code!=500){
              console.log("成功");
              this.$message({
              message: '添加教师信息成功',
              type: 'success'
              });
              this.isSuccessSubmit=true;
          }else{
              console.log("失败");
              this.$message({
              message: res.msg,
              type: 'error'
              });
          }
      })
    },
    updateTeacher(){
        request({
        url:'/teacher/update',
        method:'put',
        data:{
            id:this.teacher.id,
            headerUrl: this.teacher.headerUrl,
            name:this.teacher.name,
            sex: this.teacher.sex,
            phone: this.teacher.phone,
            password: this.teacher.password,
            qq: this.teacher.qq,
            officeName: this.teacher.officeName,
            isAdmin: this.teacher.isAdmin,
            collegeId:this.teacher.collegeId,
        }
      }).then(res=>{
         if(res.code!=500){
              console.log("成功");
              this.$message({
              message: '修改教师信息成功',
              type: 'success'
              });
              this.isSuccessSubmit=true;
          }else{
              console.log("失败");
              this.$message({
              message: res.msg,
              type: 'error'
              });
          }
      })
    },
    getTeacher(id){
      request({
        url:`/teacher/get/${id}`
      }).then(res=>{
        this.teacher=res.data.teacher
      }).catch(err=>{
        console.log(err);
        
      })
    },
       uploadFileSuccess(response, file, fileList){
        if(this.ObjectName){
            console.log("删除之前上传的文件");
            
            this.fileRemove(this.ObjectName)
        }
        this.$refs.upload.clearFiles(); 
        console.log("上传文件成功后获得的响应",response)
        let name=response.data.urlList[0].fileName;//原文件名
        let ObjectName=response.data.urlList[0].resourceName;//oss中的文件名
        this.ObjectName=ObjectName;
        let url=response.data.urlList[0].resourceUrl;//oss访问路径
        this.teacher.headerUrl=url;
    },
    uploadFileError(err, file, fileList){
        console.log("上传文件失败后获得的响应",response)
         this.$refs.upload.clearFiles(); //去掉文件列表
        this.$message({  //弹窗
          type: 'error',
          message: '导入失败！',
          center: true,
          duration: 1000
        });
    },
    fileRemove(ObjectName) {
        console.log("开始删除");
        request({
            url:'/oss/delete',
            method:'delete',
            params:{
                ObjectName:ObjectName
            }
        }).then(res=>{
            this.ObjectName=''
        }).catch(err=>{
        })
    },

      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if(!this.isUpdate){
              this.addTeacher();
            }else{
              this.updateTeacher();
            }
            
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
  this.getCollgeList();
  
  if(this.$route.params&&this.$route.params.id){
    this.title="修改教师信息"
      this.isUpdate=true;
      this.getTeacher(this.$route.params.id)
  }
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {
    //  if(this.ObjectName&&!this.isSuccessSubmit){
    //         console.log("刷新删除未提交的");
    //         this.fileRemove(this.ObjectName)
    //     }
},//生命周期 - 更新之后
beforeDestroy() {
     if(this.ObjectName&&!this.isSuccessSubmit){
            console.log("关闭删除未提交的");
            this.fileRemove(this.ObjectName)
    }
},//生命周期 - 销毁之前
destroyed() {
},
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
.header-img{
    height: 50px;
    width: 50px;
    border-radius: 25px;
}
</style>