<div>
      <el-backtop style="background-color:#000000"></el-backtop>

    <div style="background-image: url('https://cptmanagement-fzy.oss-cn-beijing.aliyuncs.com/2020-11-24/head_bg.jpg');min-width:1380px;">

    <div class="header">
        <div class="menu-list">
            <div class="left">
                <img
                src="https://cptmanagement-fzy.oss-cn-beijing.aliyuncs.com/2020-11-24/390d8773af64d305031fc406f1f9b689.jpg"/>
            </div>
            <div class="right">
                    <div class="top">
                        <div>技能竞赛系统</div>
                    </div>

                    <div class="bottom">
                        <div v-for="item in menuItemTitle" class="menu-item-title" :key="item.index">
                            <router-link :to="item.path"   class="link" :class="activePath.indexOf(item.path)!=-1?'isActive':''">{{item.name}}</router-link>
                        </div>
                            <!-- <div>首页</div>
                            <div>竞赛学院分类</div>
                            <div>竞赛获奖信息</div>
                            <div>竞赛报名入口</div>
                            <div>教师入口</div>
                            <div>个人中心</div> -->
                        <div>
                                <div v-if="student.id==-1">
                                <router-link to="/login">登入</router-link>
                                <span> | </span>
                                <router-link to="/register">注册</router-link>
                                </div>

                                <div v-else>
                                <a href="/personal">{{student.name}}</a>
                                <span> | </span>
                                <a @click="logout">退出</a>
                                </div>
                        </div>
                </div>
            </div>
        </div>
    </div>
    </div>

    <div style="background: url('http://www.hunangy.com/img/bg01.png') repeat-y top center;min-width:1380px;">
    <div class="middle" >
        
        <router-view></router-view>

    </div>
    </div>

    <div class="floor" style="min-width:1380px;">
            <div class="floor-item1">
            © CopyRight 2021, Inc.All Rights Reserved. <br> 
            学院地址：湖南省怀化市怀化学院东校区<br>
            湘教QS3-200505-000638 湘ICP备19015321号
            </div>

            <div class="floor-item2">
                <!-- http://www.hunangy.com/img/foot_logo.png -->
                 <img src="http://www.hhtc.edu.cn/img/logo.png" class="foot_logo">
                 
            </div>
           
            <div class="floor-item3">
                    <div><img src="http://www.hunangy.com/img/weixin.jpg">官方微信</div>
                    <div><img src="http://www.hunangy.com/img/weibo.png">官方微博</div>
                    <div><img src="http://www.hunangy.com/img/douyin.jpg">官方抖音</div>
            </div>
    </div>
</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../utils/request"

export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    activePath:this.$route.path,
    menuItemTitle:[
    {name:'首页',          path:'/cptlist',index:0},
    {name:'竞赛学院分类',   path:'/collegecptlist',index:1},
    {name:'竞赛获奖信息',   path:'/winninginfolist',index:2},
    {name:'竞赛报名入口',   path:'/apply',index:3},
    {name:'教师入口',       path:'/login',index:4},
    {name:'个人中心',       path:'/personal',index:5}
     ],
     student:{id:-1}
};
},
//计算属性
computed: {

},
//监控data中数据变化
watch: {
 '$route' (to, from) {
      // 对路由变化作出响应...
      this.activePath=to.path;
    }
},
//方法
methods: {
   //从cookie中拿到用户信息
   getStudentInfo(){
            if(cookie.get("student")){
            let studentInfo=cookie.get("student");
            if(studentInfo){
                this.student = JSON.parse(studentInfo)
            } 
            }
            // console.log("cpt-token",cookie.get("cpt-token"));
   },
   //退出操作 清空cookie
   logout(){
       //清空cookie值
      // cookie.set('student','',{domain: 'localhost'})
      // cookie.set('cpt-token','',{domain: 'localhost'})
      if(cookie.get('student')&&cookie.get('cpt-token')){
          request({
            url:'/login/logout',
            method:'get',
          }).then(res=>{
              if(res!=500){
                  //删除cookie
                  cookie.remove('student')
                  cookie.remove('cpt-token')
                  //回到首页面
                  window.location.href = "/";
              }
          }).catch(err=>{
              console.log(err);
          })
      }
     
   }
},
//声明周期 - 创建完成（可以访问当前this实例）
created() {
   this.getStudentInfo();
}
};
</script>

