<template>
<div>
    <el-card shadow="hover" style="min-height:500px;margin-bottom:50px">
    
        <el-form :model="formData"  :rules="rules" ref="ruleForm" label-width="100px" style="width:500px" :hide-required-asterisk="false">
        
        <div class="tilte">{{titleText}}</div>
        <el-form-item label="学院:">
            <el-select v-model="formData.collegeId" placeholder="请选择" @change="changeSelect" :disabled="!isApply">
            <el-option
                v-for="(item,index) in collegeList"
                :key="index"
                :label="item.collegeName"
                :value="item.id"
            />
            </el-select>
        </el-form-item>
        <el-form-item label="竞赛:" prop="scId" >
            <el-select v-model="formData.scId" placeholder="请选择" @change="refreshTeacher" :disabled="!isApply">
            <el-option
                v-for="(item,index) in scInfoList"
                :key="index"
                :label="item.scName"
                :value="item.id"
            />
            </el-select>
            <el-button style="margin-left:5px" v-if="formData.scId" @click="lookCptDetails" type="success" icon="el-icon-view" circle>查看详情</el-button>
        </el-form-item>

        <el-form-item v-if="teacher" label="发布老师:">
            <el-link  target="_blank" :underline="false" style="font-size:20px">
            {{teacher.name}}
            <img class="teacher-header" :src="teacher.headerUrl"/>
            </el-link>
        </el-form-item>

        <el-form-item label="申请人:">
            <el-input v-model="formData.student.name" disabled></el-input>
        </el-form-item>

        <el-form-item label="身份证:">
            <el-input v-model="formData.student.cdCard" disabled></el-input>
        </el-form-item>

        <el-form-item>
            <el-upload
                name="resources"
                action="http://localhost:9000/oss/upload"
                :on-success="uploadFileSuccess"
                :on-error="uploadFileError"
                :on-remove="fileRemove"
                :before-remove="beforeFileRemove"
                :on-preview="handlePreview"
                multiple
                :limit="1"
                :on-exceed="handleExceed"
                :file-list="fileList">
            <el-button size="small" type="primary">上传申请文件</el-button>
            <div slot="tip" class="el-upload__tip">单文件不可超过5MB,多文件总大小不可超20MB</div>
            </el-upload>
        </el-form-item>
        <el-form-item>
             <el-button v-if="isApply" type="primary" @click="apply('ruleForm')" round>提交申请表</el-button>
             <el-button v-else type="success" @click="update('ruleForm')" round>修改申请表</el-button>
        </el-form-item>
    </el-form>

    </el-card>
   
</div>

</template>

<script>
import request from "../../../utils/request"
import  cookie from 'js-cookie'

export default {
//import 引入的组件需要注入到对象中才能使用
components:{},
props:{},
data(){
//这里存数据
return{
    titleText:'填写技能竞赛申报表',
    isApply:true,
    isFirstload:true,
    collegeList:[],
    scInfoList:[],
    teacher:'',
    formData:{
        collegeId:'',
        scId:'',
        student:{},
        applyFile:''
    },
    fileList: [
       
    ],
    rules: {
          scId: [
            { required: true, message: '请选择要报名的技能竞赛', trigger: 'change' },
          ]
    }


};
},
//计算属性
computed: {
},
//监控data中数据变化
watch: {
},
//方法
//声明周期 - 创建完成（可以访问当前this实例）
created() {
    this.getBaseInfo();
    let {scId,collegeId,isUpdate,applyId}=this.$route.params;
    //this.$route.params.applyId
    if(scId&&collegeId){
        //带sc_id 是点击某个竞赛信息跳转过来的
        // this.isApply=true;
        this.isCheckScInfoInto=true;
        this.formData.scId=scId;
        this.formData.collegeId=collegeId;
    }else{
        //不带sc_id 是直接等申请入口进来的
    }
    //携带isUpdate表示要修改申请表 只能修改申请文件
    if(isUpdate){
        this.isApply=false;
        this.titleText="修改技能竞赛申报表"
        request({
            url:`/scApply/get/${applyId}`,
            method:'get'
        }).then(res=>{
            let url=res.data.scApply.applyFile//oss访问路径
            let name=res.data.scApply.applyFile.slice(97);//截取原文件名
            let ObjectName=res.data.scApply.applyFile.slice(54);//截取oss中的文件名
            this.formData.applyFile=url;
            let obj={
                name,
                url,
                ObjectName
            }
            this.fileList.push(obj)
        }).catch(err=>{
            console.log(err);
        })
    }
},
methods: {
    getBaseInfo(){
        //获取基本的数据 学院及其院下竞赛列表 学生信息
        request({
            url:'/college/collegeList',
            method:'get'
        }).then(res=>{
            console.log(res.data.collegeList)
            this.collegeList=res.data.collegeList;
            this.changeSelect();
            this.isFirstload=false;
        }).catch(err=>{
        })

        let studentInfo=cookie.get("student");
        if(studentInfo){
            this.formData.student = JSON.parse(studentInfo)
        }
    },
    changeSelect() {
        if(!this.isFirstload){
            this.formData.scId = ''
            this.teacher=''
        }
          for(let i=0;i<this.collegeList.length;i++){
              if(this.formData.collegeId==this.collegeList[i].id){
                  console.log("collegeList[i].id",this.collegeList[i].id);
                  this.scInfoList=this.collegeList[i].scInfoList;
              }
          }
          this.refreshTeacher();
    },
    refreshTeacher(){
        for(let i=0;i<this.scInfoList.length;i++){
            if(this.formData.scId==this.scInfoList[i].id){
                this.teacher=this.scInfoList[i].teacher;
            }
        }
    },
    uploadFileSuccess(response, file, fileList){
        console.log("上传文件成功后获得的响应",response)
        let name=response.data.urlList[0].fileName;//原文件名
        let ObjectName=response.data.urlList[0].resourceName;//oss中的文件名
        let url=response.data.urlList[0].resourceUrl;//oss访问路径
        this.formData.applyFile=url;
        let obj={
                name,
                url,
                ObjectName
        }
        this.fileList.push(obj)
    },
    uploadFileError(err, file, fileList){
        console.log("上传文件失败后获得的响应",response)
    },
    beforeFileRemove(file, fileList) {
        console.log("删除之前询问")
        return this.$confirm(`确定移除 ${ file.name }？`);
    },
    fileRemove(file, fileList) {
        console.log(this.fileList[0].ObjectName);
        
        request({
            url:'/oss/delete',
            method:'delete',
            params:{
                ObjectName:this.fileList[0].ObjectName
            }
        }).then(res=>{
            this.fileList=[]
            this.formData.applyFile=''
        }).catch(err=>{
        })
    },
    handlePreview(file) {
        console.log()
        console.log("handlePreview",file);
    },
    handleExceed(files, fileList) {
        this.$message.warning(`当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
    },
    apply(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.formData.applyFile){
                request({
                    url:'/scApply/apply',
                    method:'post',
                    data:{
                        scId:this.formData.scId,
                        studentId:this.formData.student.id,
                        applyFile:this.formData.applyFile,
                        status:0
                    }
                }).then(res=>{
                    this.$message({
                        message: '申请信息提交成功',
                        type: 'success'
                    });
                     this.$router.push({
                        name: 'myapplylist',
                    })
                }).catch(err=>{
                    this.$message.error('申请信息提交失败了！');
                })
            }else{
                 this.$message({
                        message: '请上传申请文件',
                        type: 'warning'
                    });
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    },
    update(formName){
        let applyId=this.$route.params.applyId;
         this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.formData.applyFile){
                request({
                    url:'/scApply/update',
                    method:'put',
                    data:{
                        id:applyId,
                        scId:this.formData.scId,
                        studentId:this.formData.student.id,
                        applyFile:this.formData.applyFile,
                        status:0
                    }
                }).then(res=>{
                    this.$message({
                        message: '修改申请信息成功',
                        type: 'success'
                    });
                     this.$router.push({
                        name: 'myapplylist',
                    })
                }).catch(err=>{
                    this.$message.error('修改申请信息失败了！');
                })
            }else{
                 this.$message({
                        message: '请上传申请文件',
                        type: 'warning'
                    });
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    },
    lookCptDetails(){
        this.$router.push(`/cptdetails/${this.formData.scId}`)
    },
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {},//生命周期 - 创建之前
beforeMount() {},//生命周期 - 挂载之前
beforeUpdate() {},//声明周期 - 更新之前
updated() {},//生命周期 - 更新之后
beforeDestroy() {},//生命周期 - 销毁之前
destroyed() {},//生命周期 - 销毁之后
activated() {},//缓存keep-alive
};
</script>

<style scoped>
.tilte{
    margin-bottom: 10px;
}
.teacher-header{
    display: inline-block;
    height: 40px;
    width: 40px;
    border-radius: 20px;
    vertical-align:middle
}
</style>